// ** React Imports
import { useEffect, useState, Fragment } from 'react'
import { useForm, Controller, useFieldArray } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
// ** Third Party Components
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
const MySwal = withReactContent(Swal)
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
// ** Reactstrap Imports
import { Card, CardBody, Row, Col, Label, FormFeedback, Modal, ModalBody, Button, ModalHeader, Input, CardText, ModalFooter } from 'reactstrap'
import { useDispatch } from 'react-redux'
import Select from 'react-select'
import axios from '@src/configs/axios/axiosConfig'
import { addTask } from '../store/index'
//import toast from 'react-hot-toast'
import classnames from 'classnames'

import Flatpickr from 'react-flatpickr'
import 'react-slidedown/lib/slidedown.css'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/base/pages/app-invoice.scss'
import { Plus, Trash2 } from 'react-feather'
import moment from 'moment'

const priorityOptions = [
  { id: 1, name: "Low" },
  { id: 2, name: "Medium" },
  { id: 3, name: "High" }
]

const durationOptions = [
  { id: 1, name: "Monthly" },
  { id: 2, name: "Yearly" },
  { id: 3, name: "Quarterly" },
  { id: 4, name: "Daily" },
  { id: 5, name: "Weekly" }
]

const colourStyles = {
  multiValue: (styles, { }) => {

    return {
      ...styles,
      backgroundColor: '#7367f0'
    }
  }
}
const TaskList = ({ index, clientOptions, setValue, userOptions, errors, control, removeItem }) => {

  const [dateList, setDateList] = useState([])
  const dayList = [{ id: 7, name: 'Sunday' }, { id: 1, name: 'Monday' }, { id: 2, name: 'Tuesday' }, { id: 3, name: 'Wednesday' }, { id: 4, name: 'Thursday' }, { id: 5, name: 'Friday' }, { id: 6, name: 'Saturday' }]
  useEffect(() => {
    setDateList(Array(30).fill().map((num, i) => { return { id: i + 1, name: i + 1 } }))
  }, [])

  const [assigneeUserOptions, setAssigneeUserOptions] = useState(userOptions)
  const [duration, setDuration] = useState(1)
  const [reviewerUserOptions, setReviewerUserOptions] = useState(userOptions)
  const quarterlyDatesList = [
    {
      label: "Quarter (Apr - Jun)",
      startDate: null,
      endDate: null
    }, {
      label: "Quarter (Jul - Sep)",
      startDate: null,
      endDate: null
    }, {
      label: "Quarter (Oct - Dec)",
      startDate: null,
      endDate: null
    }, {
      label: "Quarter (Jan - Mar)",
      startDate: null,
      endDate: null
    }
  ]
  useEffect(() => {
    setAssigneeUserOptions(userOptions)
    setReviewerUserOptions(userOptions)
  }, [userOptions])

  const handleQuarDateChange = (val, ind, fieldName, k) => {

    const res = moment(val[0]).startOf('d').valueOf()
    setValue(`recurringTasks[${ind}].quarterlyDates[${k}].${fieldName}`, res)

  }

  const handleDateChange = (val, ind, fieldName) => {

    let res = ''
    const today = moment().format('D')
    if (duration === 2) {
      res = moment(val[0]).startOf('d').valueOf()
    } else if (duration === 5) {
      const today_day = moment().format('d')
      if (parseInt(today_day) >= val) {
        res = moment().day(val).add(1, 'w').startOf('d').valueOf()
      } else {
        res = moment().day(val).startOf('d').valueOf()
      }
    } else if (duration === 1) {
      if (parseInt(today) <= val) {
        res = moment().set('D', val).startOf('d').valueOf()
      } else {
        res = moment().set('D', val).add(1, 'M').startOf('d').valueOf()
      }
    }

    setValue(`recurringTasks[${ind}].${fieldName}`, res)

  }
  const handleAssigneeChange = (e, ind) => {
    const tempArr = Array.isArray(e) ? e.map(x => x.id) : []
    let reviewerOptions
    if (tempArr.length > 0) {
      reviewerOptions = userOptions.filter(({ id: id1 }) => !tempArr.some(id2 => id2 === id1))
    } else { reviewerOptions = userOptions }

    setReviewerUserOptions(reviewerOptions)
    setValue(`recurringTasks[${ind}].assigneeId`, tempArr)
  }

  const handleReviwerChange = (e, ind) => {
    const tempArr = Array.isArray(e) ? e.map(x => x.id) : []
    let assigneeOptions
    if (tempArr.length > 0) {
      assigneeOptions = userOptions.filter(({ id: id1 }) => !tempArr.some(id2 => id2 === id1))
    } else { assigneeOptions = userOptions }

    setAssigneeUserOptions(assigneeOptions)
    setValue(`recurringTasks[${ind}].reviewerId`, tempArr)
  }

  const handleDuration = (id, ind) => {
    setDuration(id)
    if (id === 4) {
      const date = moment().add(1, 'd').startOf('d').valueOf()
      setValue(`recurringTasks[${ind}].startDate`, date)
      setValue(`recurringTasks[${ind}].endDate`, date)
    }
    if (id === 4 || id === 5) {
      setValue(`recurringTasks[${ind}].invoiceAmount`, 0)
      setValue(`recurringTasks[${ind}].invoiceFlag`, false)
    }
  }

  return (
    <Fragment>

      <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
        <CardText className='col-title mb-md-50 mb-0'>Client</CardText>
        <Controller
          control={control}
          name={`recurringTasks[${index}].clientId`}
          id={`recurringTasks[${index}].clientId`}
          render={({ field, ref }) => (
            <Select
              {...field}
              inputRef={ref}
              className={classnames('react-select', { 'is-invalid': errors?.recurringTasks })}
              {...field}
              classNamePrefix='select'
              options={clientOptions}
              placeholder='Select Client'
              value={clientOptions.find(c => { return c.id === field.value })}
              onChange={val => { field.onChange(val.id) }}
              getOptionLabel={(option) => option.name}
              getOptionValue={(option) => option.id}
              styles={colourStyles}
            />
          )}

        />
        {errors?.recurringTasks && <FormFeedback className='text-danger'>{errors?.recurringTasks[index]?.clientId?.message}</FormFeedback>}
      </Col>

      <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-1 col-sm-3'>
        <CardText className='col-title mb-md-50 mb-0'>Priority</CardText>
        <Controller
          control={control}
          name={`recurringTasks[${index}].priority`}
          id={`recurringTasks[${index}].priority`}
          render={({ field, value, ref }) => (
            <Select
              {...field}
              inputRef={ref}
              className={classnames('react-select', { 'is-invalid': errors?.recurringTasks })}
              {...field}
              classNamePrefix='select'
              placeholder='Priority'
              options={priorityOptions}
              value={priorityOptions.find(c => { return c.id === value })}
              onChange={val => field.onChange(val.id)}
              getOptionLabel={(option) => option.name}
              getOptionValue={(option) => option.id}
            />
          )}

        />
        {errors?.recurringTasks && <FormFeedback className='text-danger'>{errors?.recurringTasks[index]?.priority?.message}</FormFeedback>}
      </Col>
      <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-3 col-sm-4'>
        <CardText className='col-title mb-md-50 mb-0'>Assignee</CardText>
        <Controller
          control={control}
          name="assignee"
          id="assignee"
          render={({ field, value, ref }) => (
            <Select
              inputRef={ref}
              className={classnames('react-select', { 'is-invalid': errors?.recurringTasks })}
              {...field}
              classNamePrefix='select'
              options={assigneeUserOptions}
              isMulti={true}
              value={value} // set selected values
              placeholder='Select Assignee'
              onChange={(e) => { handleAssigneeChange(e, index) }}
              getOptionLabel={(option) => option.name}
              getOptionValue={(option) => option.id}
              styles={colourStyles}
            />
          )}

        />
        {errors?.recurringTasks && <FormFeedback className='text-danger'>{errors?.recurringTasks[index]?.assignee?.message}</FormFeedback>}
      </Col>
      <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-3 col-sm-4'>
        <CardText className='col-title mb-md-50 mb-0'>Reviewer</CardText>
        <Controller
          control={control}
          name="reviewer"
          id="reviewer"
          render={({ field, value, ref }) => (
            <Select
              inputRef={ref}
              className={classnames('react-select', { 'is-invalid': errors?.recurringTasks })}
              {...field}
              classNamePrefix='select'
              options={reviewerUserOptions}
              isMulti={true}
              value={value} // set selected values
              placeholder='Select Reviewer'
              onChange={(e) => { handleReviwerChange(e, index) }}
              getOptionLabel={(option) => option.name}
              getOptionValue={(option) => option.id}
              styles={colourStyles}
            />
          )}
        />
        {errors.reviewer && <FormFeedback className='text-danger'>{errors?.recurringTasks[index]?.reviewer?.message}</FormFeedback>}
      </Col>
      <Col>
        <CardText className='col-title mb-md-50 mb-0'>Recurring Invoice</CardText>
        <div className='form-switch form-check-primary'>
          <Controller
            control={control}
            name={`recurringTasks[${index}].invoiceFlag`}
            id={`recurringTasks[${index}].invoiceFlag`}
            render={({ field, value }) => (
              <Input
                value={value}
                type='switch'
                {...field}
                name={`recurringTasks[${index}]?.invoiceFlag`} />
            )}
          />
        </div>
      </Col>
      <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-1 col-sm-2'>
        <CardText className='col-title mb-md-50 mb-0'>Amount</CardText>
        <Controller
          control={control}
          name={`recurringTasks[${index}].invoiceAmount`}
          id={`recurringTasks[${index}].invoiceAmount`}
          render={({ field, value }) => (
            <Input
              placeholder='Amount'
              value={value}
              type='text'
              {...field}
            />
          )}
        />
        {errors?.recurringTasks && <p className='text-danger'>{errors?.recurringTasks[index]?.invoiceAmount?.message}</p>}
      </Col>
      <Col className='mt-1 d-flex' md={12}>
        <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
          <Controller
            control={control}
            name={`recurringTasks[${index}].recurringId`}
            id={`recurringTasks[${index}].recurringId`}
            render={({ field, value, ref }) => (
              <Select
                {...field}
                inputRef={ref}
                className={classnames('react-select', { 'is-invalid': errors?.recurringTasks })}
                {...field}
                classNamePrefix='select'
                options={durationOptions}
                placeholder='Select Frequency'
                value={durationOptions.find(c => { return c.id === value })}
                onChange={val => { field.onChange(val.id); handleDuration(val.id, index) }}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
              />
            )}

          />
          {errors?.recurringTasks && <FormFeedback className='text-danger'>{errors?.recurringTasks[index]?.recurringId?.message}</FormFeedback>}
        </Col>

        {
          duration === 1 &&
          <Fragment>
            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
              <Controller
                control={control}
                name={`recurringTasks[${index}].startDate`}
                id={`recurringTasks[${index}].startDate`}
                render={({ field, ref }) => (
                  <Select
                    {...field}
                    inputRef={ref}
                    className={classnames('react-select', { 'is-invalid': errors?.recurringTasks })}
                    {...field}
                    classNamePrefix='select'
                    options={dateList}
                    placeholder='Select Date'
                    value={dateList.find(c => { return c.id === field.value })}
                    onChange={val => { field.onChange(val.id); handleDateChange(val.id, index, 'startDate') }}
                    getOptionLabel={(option) => option.name}
                    getOptionValue={(option) => option.id}
                  />
                )}

              />
              {errors?.recurringTasks && <FormFeedback className='text-danger'>{errors?.recurringTasks[index]?.startDate?.message}</FormFeedback>}
            </Col>

            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
              <Controller
                control={control}
                name={`recurringTasks[${index}].endDate`}
                id={`recurringTasks[${index}].endDate`}
                render={({ field, ref }) => (
                  <Select
                    {...field}
                    inputRef={ref}
                    className={classnames('react-select', { 'is-invalid': errors?.recurringTasks })}
                    {...field}
                    classNamePrefix='select'
                    placeholder='Select Date'
                    options={dateList}
                    value={dateList.find(c => { return c.id === field.value })}
                    onChange={val => { field.onChange(val.id); handleDateChange(val.id, index, 'endDate') }}
                    getOptionLabel={(option) => option.name}
                    getOptionValue={(option) => option.id}
                  />
                )}

              />
              {errors?.recurringTasks && <FormFeedback className='text-danger'>{errors?.recurringTasks[index]?.endDate?.message}</FormFeedback>}
            </Col>
          </Fragment>
        }
        {
          duration === 5 &&
          <Fragment>
            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
              <Controller
                control={control}
                name={`recurringTasks[${index}].startDate`}
                id={`recurringTasks[${index}].startDate`}
                render={({ field, ref }) => (
                  <Select
                    {...field}
                    inputRef={ref}
                    className={classnames('react-select', { 'is-invalid': errors?.recurringTasks })}
                    {...field}
                    classNamePrefix='select'
                    options={dayList}
                    placeholder='Select Date'
                    value={dayList.find(c => { return c.id === field.value })}
                    onChange={val => { field.onChange(val.id); handleDateChange(val.id, index, 'startDate') }}
                    getOptionLabel={(option) => option.name}
                    getOptionValue={(option) => option.id}
                  />
                )}

              />
              {errors?.recurringTasks && <FormFeedback className='text-danger'>{errors?.recurringTasks[index]?.startDate?.message}</FormFeedback>}
            </Col>
            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
              <Controller
                control={control}
                name={`recurringTasks[${index}].endDate`}
                id={`recurringTasks[${index}].endDate`}
                render={({ field, ref }) => (
                  <Select
                    {...field}
                    inputRef={ref}
                    className={classnames('react-select', { 'is-invalid': errors?.recurringTasks })}
                    {...field}
                    classNamePrefix='select'
                    placeholder='Select Date'
                    options={dayList}
                    value={dayList.find(c => { return c.id === field.value })}
                    onChange={val => { field.onChange(val.id); handleDateChange(val.id, index, 'endDate') }}
                    getOptionLabel={(option) => option.name}
                    getOptionValue={(option) => option.id}
                  />
                )}

              />
              {errors?.recurringTasks && <FormFeedback className='text-danger'>{errors?.recurringTasks[index]?.endDate?.message}</FormFeedback>}
            </Col>
          </Fragment>
        }
        {
          duration === 2 &&
          <Fragment>
            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
              <Controller
                name={`recurringTasks[${index}].startDate`}
                id={`recurringTasks[${index}].startDate`}
                control={control}
                rules={{ required: true }}
                options={{ dateFormat: "d-m-Y" }}
                render={({ field }) => (
                  <Flatpickr
                    min={moment().add(1, 'D').format('Y-m-d')}
                    value={field.value}
                    placeholder='Select Date'
                    onChange={(date, dateStr) => { field.onChange(dateStr); handleDateChange(date, index, 'startDate') }}
                    options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                    className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.startDate })} />
                )}
              />
              {errors?.recurringTasks && <FormFeedback className='text-danger'>{errors?.recurringTasks[index]?.startDate?.message}</FormFeedback>}
            </Col>

            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
              <Controller
                onChange={date => setDate(date)}
                name={`recurringTasks[${index}].endDate`}
                id={`recurringTasks[${index}].endDate`}
                control={control}
                rules={{ required: true }}
                options={{ dateFormat: "d-m-Y" }}
                render={({ field }) => (
                  <Flatpickr
                    min={moment().add(1, 'D').format('Y-m-d')}
                    value={field.value}
                    placeholder='Select Date'
                    onChange={(date, dateStr) => { field.onChange(dateStr); handleDateChange(date, index, 'endDate') }}
                    options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                    className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.endDate })} />
                )}
              />

              {errors?.recurringTasks && <FormFeedback className='text-danger'>{errors?.recurringTasks[index]?.endDate?.message}</FormFeedback>}
            </Col>

          </Fragment>
        }{
          duration === 3 &&
          <Col md={6} >
            {
              quarterlyDatesList.map((item, k) => {
                return (
                  <Col className='d-flex mb-1' key={k}>
                    <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-4 col-sm-3'>
                      <Controller
                        name={`recurringTasks[${index}].quarterlyDates[${k}].startDate`}
                        id={`recurringTasks[${index}].quarterlyDates[${k}].startDate`}
                        control={control}
                        rules={{ required: true }}
                        options={{ dateFormat: "d-m-Y" }}
                        render={({ field }) => (
                          <Flatpickr
                            value={field.value}
                            placeholder='Select Date'
                            onChange={(date, dateStr) => { field.onChange(dateStr); handleQuarDateChange(date, index, 'startDate', k) }}
                            options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                            className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.startDate })} />
                        )}
                      />
                      {errors?.recurringTasks && <FormFeedback className='text-danger'>{errors?.recurringTasks[index]?.startDate?.message}</FormFeedback>}
                    </Col>

                    <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-4 col-sm-3'>
                      <Controller
                        onChange={date => setDate(date)}
                        name={`recurringTasks[${index}].quarterlyDates[${k}].endDate`}
                        id={`recurringTasks[${index}].quarterlyDates[${k}].endDate`}
                        control={control}
                        rules={{ required: true }}
                        options={{ dateFormat: "d-m-Y" }}
                        render={({ field }) => (
                          <Flatpickr
                            value={field.value}
                            placeholder='Select Date'
                            onChange={(date, dateStr) => { field.onChange(dateStr); handleQuarDateChange(date, index, 'endDate', k) }}
                            options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                            className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.endDate })} />
                        )}
                      />

                      {errors?.recurringTasks && <FormFeedback className='text-danger'>{errors?.recurringTasks[index]?.endDate?.message}</FormFeedback>}
                    </Col>
                    <Col>{item.label}</Col>
                  </Col>
                )
              })
            }
          </Col>
        }
        <Col lg={1}>
          <div className='d-lg-flex justify-content-center invoice-product-actions pt-1 px-25'>
            <Trash2 size={16} className='text-danger' onClick={() => { removeItem(index) }} />
          </div>
        </Col>
      </Col>
    </Fragment>

  )
}

const RecurringTasks = (props) => {

  const [block, setBlock] = useState(false)
  const [formModal, setFormModal] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const dispatch = useDispatch()
  const { id, addTaskFlag, selectedService, refreshList } = props

  // ** States
  const schema = yup.object().shape({
    recurringTasks: yup.array().of(
      yup.object().shape({
        clientId: yup.string().required("Please Select Client"),
        createdBy: yup.string().default(userId),
        organizationId: yup.string().default(activeOrgId),
        serviceId: yup.string().default(id),
        invoiceFlag: yup.boolean().default(false),
        recurringId: yup.number().default(1).required("Please Select Frequency"),
        priority: yup.number().default(1).required("Please Select Priority"),
        startDate: yup.string().when("recurringId", { is: (recurringId) => (recurringId === 1 || recurringId === 2 || recurringId === 5), then: yup.string().required("Please Enter Start Date") }),
        endDate: yup.string().when("recurringId", { is: (recurringId) => (recurringId === 1 || recurringId === 2 || recurringId === 5), then: yup.string().required("Please Enter End Date") }),
        assigneeId: yup.array().min(1, 'Atleast require ine Selection'),
        reviewerId: yup.array(),
        invoiceAmount: yup.string().default('0.00').nullable(),
        quarterlyDates: yup.array().of(
          yup.object().shape({
            startDate: yup.string().when("duration", { is: (duration) => parseInt(duration) === 3, then: yup.string().required("Please Enter Start Date") }),
            endDate: yup.string().when("duration", { is: (duration) => parseInt(duration) === 3, then: yup.string().required("Please Enter End Date") })
          })
        )
      })
    ).min(1, "Require Atleast one Item to create task.")

  })

  const { handleSubmit, formState: { errors }, control, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const { fields, append, remove } = useFieldArray({ name: 'recurringTasks', control, keyName: "rowid" })

  const addItem = () => {

    append({})
  }
  const [userOptions, setUserOptions] = useState([])
  const [clientOptions, setClientOptions] = useState([])
  const getClients = () => {
    setBlock(true)
    axios.post('/clients/dropdown', { status: 1 }).then(response => {
      const arr = response.data
      setBlock(false)
      setClientOptions(arr.clients)
    })
  }

  const getOrganizationUsers = async () => {
    axios.post('/organizationusers/dropdown').then(response => {
      const arr = response.data
      setUserOptions(arr.organizationusers)
    }).catch((err) => {
      console.log(err)
    })

  }

  useEffect(async () => {
    await getOrganizationUsers()
    await getClients()
    await addItem()
  }, [])


  const removeItem = (ind) => {
    remove(ind)
  }

  useEffect(() => {
    setFormModal(addTaskFlag)
  }, [addTaskFlag])

  const onSubmit = async data => {
    setBlock(true)
    await dispatch(addTask(data))
    setFormModal(false)
    refreshList(true)
    setBlock(false)
    remove()
  }
  return (

    <Fragment>
      <Modal scrollable className='modal-dialog-centered modal-xl' isOpen={formModal} toggle={() => setFormModal(!formModal)}>
        <ModalBody>
          <Col className='d-flex justify-content-between'>
            <Col><h5>Add Recurring Task - <span className='text-primary'>{selectedService?.name}</span></h5></Col>
          </Col>
          <form onSubmit={handleSubmit(onSubmit)}>

            {Object.keys(errors).map((obj, k) => {
              return <FormFeedback key={k}> {obj}</FormFeedback>
            })}
            <div className='invoice-add'>
              <div className='invoice-preview-card'>
                <UILoader blocking={block} loader={<Loader />}>
                  <CardBody className='invoice-padding invoice-product-details min-vh-100'>
                    {

                      fields.map((item, index) => {
                        return (
                          <div key={item.rowid} className='repeater-wrapper'>
                            <Row>
                              <Col className='d-lg-flex border p-2 position-relative rounded flex-wrap' sm='12'>
                                <TaskList item={item} index={index} clientOptions={clientOptions} userOptions={userOptions} setValue={setValue} setBlock={setBlock} errors={errors} control={control} removeItem={removeItem} />
                              </Col>
                            </Row>
                          </div>
                        )
                      })
                    }
                    <Button color='primary' outline size='sm' onClick={() => { addItem() }}>
                      <Plus size={15} /> Add New
                    </Button>
                  </CardBody>
                </UILoader>
              </div>
            </div>
          </form >
        </ModalBody>
        <ModalFooter>
          <Col className='d-flex justify-content-between'>
            <Col></Col>
            <Col className='d-flex justify-content-between'>
              <Col>
              </Col>
              <Col>
                <Button color='warning' onClick={() => { setFormModal(false); refreshList(false) }}>Cancel</Button>
                <Button color='primary' className='ms-50' type='submit' onClick={handleSubmit(onSubmit)}>Submit</Button>
              </Col>
            </Col>
          </Col>
        </ModalFooter>
      </Modal>
    </Fragment>

  )
}

export default RecurringTasks
