// ** React Imports
import { useEffect, useState, Fragment } from 'react'
import { useForm, Controller, useFieldArray } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
// ** Third Party Components
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import { Editor } from 'react-draft-wysiwyg'
import draftToHtml from 'draftjs-to-html'
import { convertToRaw } from 'draft-js'
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
// ** Reactstrap Imports
import { Card, CardBody, Row, Col, Label, FormFeedback, Modal, ModalBody, Button, ModalHeader, Input, CardText, ModalFooter } from 'reactstrap'
import { useDispatch } from 'react-redux'
import Select from 'react-select'
import axios from '@src/configs/axios/axiosConfig'
import { addCompliance } from '../store/index'
//import toast from 'react-hot-toast'
import classnames from 'classnames'

import Flatpickr from 'react-flatpickr'
import 'react-slidedown/lib/slidedown.css'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/base/pages/app-invoice.scss'
import { Plus, Trash2 } from 'react-feather'
import moment from 'moment'

const durationOptions = [
  { id: 1, name: "Monthly" },
  { id: 2, name: "Yearly" },
  { id: 3, name: "Quarterly" }
]

const colourStyles = {
  multiValue: (styles, { }) => {

    return {
      ...styles,
      backgroundColor: '#7367f0'
    }
  }
}
const InvoiceList = ({ index, clientOptions, setValue, errors, control, removeItem }) => {

  const [dateList, setDateList] = useState([])

  useEffect(() => {
    setDateList(Array(30).fill().map((num, i) => { return { id: i + 1, name: i + 1 } }))
  }, [])

  const [duration, setDuration] = useState(1)
  const quarterlyDatesList = [
    {
      label: "Quarter (Apr - Jun)",
      invoiceDate: null,
      dueDate: null
    }, {
      label: "Quarter (Jul - Sep)",
      invoiceDate: null,
      dueDate: null
    }, {
      label: "Quarter (Oct - Dec)",
      invoiceDate: null,
      dueDate: null
    }, {
      label: "Quarter (Jan - Mar)",
      invoiceDate: null,
      dueDate: null
    }
  ]

  const reminderOptions = [
    { name: '1 day before', id: 1, value: 1 },
    { name: "2 day's before", id: 2, value: 2 },
    { name: "3 day's before", id: 3, value: 3 },
    { name: "4 day's before", id: 4, value: 4 },
    { name: "5 day's before", id: 5, value: 5 },
    { name: "7 day's before", id: 6, value: 7 },
    { name: "10 day's before", id: 7, value: 10 },
    { name: "15 day's before", id: 8, value: 15 },
    { name: "21 day's before", id: 9, value: 21 },
    { name: "30 day's before", id: 10, value: 30 },
    { name: "2 month's before", id: 11, value: 60 },
    { name: "3 months's before", id: 12, value: 90 },
    { name: "4 months's before", id: 13, value: 120 }
  ]
  const [selectedReminders, setSelectedReminders] = useState([])
  const handleReminder = (e) => {
    const tempArr = Array.isArray(e) ? e.map(x => x.value) : []
    setValue("reminderOnActual", tempArr)
    setSelectedReminders(e)
  }
  const handleQuarDateChange = (val, ind, fieldName, k) => {

    const res = moment(val[0]).startOf('d').valueOf()
    setValue(`recuurilngAlerts[${ind}].quarterlyDates[${k}].${fieldName}`, res)
  }

  const handleDateChange = (val, ind, fieldName) => {

    let res = ''
    const today = moment().format('D')
    if (duration === 2) {
      res = moment(val[0]).startOf('d').valueOf()
    } else if (duration === 1) {
      if (parseInt(today) <= val) {
        res = moment().set('D', val).startOf('d').valueOf()
      } else {
        res = moment().set('D', val).add(1, 'M').startOf('d').valueOf()
      }
    }

    setValue(`recuurilngAlerts[${ind}].${fieldName}`, res)

  }
  const handleDuration = (id) => {
    setDuration(id)
  }

  return (
    <Fragment>

      <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-3 col-sm-3'>
        <CardText className='col-title mb-md-50 mb-0'>Client</CardText>
        <Controller
          control={control}
          name={`recuurilngAlerts[${index}].clientId`}
          id={`recuurilngAlerts[${index}].clientId`}
          render={({ field, ref }) => (
            <Select
              {...field}
              inputRef={ref}
              className={classnames('react-select', { 'is-invalid': errors?.recuurilngAlerts })}
              {...field}
              classNamePrefix='select'
              options={clientOptions}
              placeholder='Select Client'
              value={clientOptions.find(c => { return c.id === field.value })}
              onChange={val => { field.onChange(val.id) }}
              getOptionLabel={(option) => option.name}
              getOptionValue={(option) => option.id}
              styles={colourStyles}
            />
          )}

        />
        {errors?.recuurilngAlerts && <FormFeedback className='text-danger'>{errors?.recuurilngAlerts[index]?.clientId?.message}</FormFeedback>}
      </Col>

      <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-4 col-sm-3'>
        <Controller
          control={control}
          name="recurringId"
          id="recurringId"
          render={({ field, ref }) => (
            <Select
              {...field}
              inputRef={ref}
              className={classnames('react-select', { 'is-invalid': errors.recurringId })}
              {...field}
              classNamePrefix='select'
              isMulti
              options={reminderOptions}
              value={selectedReminders}
              onChange={handleReminder}
              getOptionLabel={(option) => option.name}
              getOptionValue={(option) => option.id}
              styles={colourStyles}
              isOptionDisabled={() => selectedReminders.length >= 3}
            />
          )}
        />
        {errors.recurringId && <FormFeedback className='text-danger'>{errors.recurringId?.message}</FormFeedback>}
      </Col>
      <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
        <Controller
          control={control}
          name={`recuurilngAlerts[${index}].recurringId`}
          id={`recuurilngAlerts[${index}].recurringId`}
          render={({ field, value, ref }) => (
            <Select
              {...field}
              inputRef={ref}
              className={classnames('react-select', { 'is-invalid': errors?.recuurilngAlerts })}
              {...field}
              classNamePrefix='select'
              options={durationOptions}
              placeholder='Select Frequency'
              value={durationOptions.find(c => { return c.id === value })}
              onChange={val => { field.onChange(val.id); handleDuration(val.id, index) }}
              getOptionLabel={(option) => option.name}
              getOptionValue={(option) => option.id}
            />
          )}

        />
        {errors?.recuurilngAlerts && <FormFeedback className='text-danger'>{errors?.recuurilngAlerts[index]?.recurringId?.message}</FormFeedback>}
      </Col>
      <Col className='mt-1 d-flex' md={12}>
        {
          duration === 1 &&
          <Fragment>
            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
              <Controller
                control={control}
                name={`recuurilngAlerts[${index}].invoiceDate`}
                id={`recuurilngAlerts[${index}].invoiceDate`}
                render={({ field, ref }) => (
                  <Select
                    {...field}
                    inputRef={ref}
                    className={classnames('react-select', { 'is-invalid': errors?.recuurilngAlerts })}
                    {...field}
                    classNamePrefix='select'
                    options={dateList}
                    placeholder='Select Date'
                    value={dateList.find(c => { return c.id === field.value })}
                    onChange={val => { field.onChange(val.id); handleDateChange(val.id, index, 'invoiceDate') }}
                    getOptionLabel={(option) => option.name}
                    getOptionValue={(option) => option.id}
                  />
                )}

              />
              {errors?.recuurilngAlerts && <FormFeedback className='text-danger'>{errors?.recuurilngAlerts[index]?.invoiceDate?.message}</FormFeedback>}
            </Col>

            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
              <Controller
                control={control}
                name={`recuurilngAlerts[${index}].dueDate`}
                id={`recuurilngAlerts[${index}].dueDate`}
                render={({ field, ref }) => (
                  <Select
                    {...field}
                    inputRef={ref}
                    className={classnames('react-select', { 'is-invalid': errors?.recuurilngAlerts })}
                    {...field}
                    classNamePrefix='select'
                    placeholder='Select Date'
                    options={dateList}
                    value={dateList.find(c => { return c.id === field.value })}
                    onChange={val => { field.onChange(val.id); handleDateChange(val.id, index, 'dueDate') }}
                    getOptionLabel={(option) => option.name}
                    getOptionValue={(option) => option.id}
                  />
                )}

              />
              {errors?.recuurilngAlerts && <FormFeedback className='text-danger'>{errors?.recuurilngAlerts[index]?.dueDate?.message}</FormFeedback>}
            </Col>
          </Fragment>
        }

        {
          duration === 2 &&
          <Fragment>
            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
              <Controller
                name={`recuurilngAlerts[${index}].invoiceDate`}
                id={`recuurilngAlerts[${index}].invoiceDate`}
                control={control}
                rules={{ required: true }}
                options={{ dateFormat: "d-m-Y" }}
                render={({ field }) => (
                  <Flatpickr
                    min={moment().add(1, 'D').format('Y-m-d')}
                    value={field.value}
                    placeholder='Select Date'
                    onChange={(date, dateStr) => { field.onChange(dateStr); handleDateChange(date, index, 'invoiceDate') }}
                    options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                    className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.invoiceDate })} />
                )}
              />
              {errors?.recuurilngAlerts && <FormFeedback className='text-danger'>{errors?.recuurilngAlerts[index]?.invoiceDate?.message}</FormFeedback>}
            </Col>

            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
              <Controller
                onChange={date => setDate(date)}
                name={`recuurilngAlerts[${index}].dueDate`}
                id={`recuurilngAlerts[${index}].dueDate`}
                control={control}
                rules={{ required: true }}
                options={{ dateFormat: "d-m-Y" }}
                render={({ field }) => (
                  <Flatpickr
                    min={moment().add(1, 'D').format('Y-m-d')}
                    value={field.value}
                    placeholder='Select Date'
                    onChange={(date, dateStr) => { field.onChange(dateStr); handleDateChange(date, index, 'dueDate') }}
                    options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                    className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.dueDate })} />
                )}
              />

              {errors?.recuurilngAlerts && <FormFeedback className='text-danger'>{errors?.recuurilngAlerts[index]?.dueDate?.message}</FormFeedback>}
            </Col>

          </Fragment>
        }{
          duration === 3 &&
          <Col md={6} >
            {
              quarterlyDatesList.map((item, k) => {
                return (
                  <Col className='d-flex mb-1' key={k}>
                    <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-4 col-sm-3'>
                      <Controller
                        name={`recuurilngAlerts[${index}].quarterlyDates[${k}].invoiceDate`}
                        id={`recuurilngAlerts[${index}].quarterlyDates[${k}].invoiceDate`}
                        control={control}
                        rules={{ required: true }}
                        options={{ dateFormat: "d-m-Y" }}
                        render={({ field }) => (
                          <Flatpickr
                            value={field.value}
                            placeholder='Select Date'
                            onChange={(date, dateStr) => { field.onChange(dateStr); handleQuarDateChange(date, index, 'invoiceDate', k) }}
                            options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                            className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.invoiceDate })} />
                        )}
                      />
                      {errors?.recuurilngAlerts && <FormFeedback className='text-danger'>{errors?.recuurilngAlerts[index]?.invoiceDate?.message}</FormFeedback>}
                    </Col>

                    <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-4 col-sm-3'>
                      <Controller
                        onChange={date => setDate(date)}
                        name={`recuurilngAlerts[${index}].quarterlyDates[${k}].dueDate`}
                        id={`recuurilngAlerts[${index}].quarterlyDates[${k}].dueDate`}
                        control={control}
                        rules={{ required: true }}
                        options={{ dateFormat: "d-m-Y" }}
                        render={({ field }) => (
                          <Flatpickr
                            value={field.value}
                            placeholder='Select Date'
                            onChange={(date, dateStr) => { field.onChange(dateStr); handleQuarDateChange(date, index, 'dueDate', k) }}
                            options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                            className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.dueDate })} />
                        )}
                      />

                      {errors?.recuurilngAlerts && <FormFeedback className='text-danger'>{errors?.recuurilngAlerts[index]?.dueDate?.message}</FormFeedback>}
                    </Col>
                    <Col>{item.label}</Col>
                  </Col>
                )
              })
            }
          </Col>
        }
        <Col lg={1}>
          <div className='d-lg-flex justify-content-center invoice-product-actions pt-1 px-25'>
            <Trash2 size={16} className='text-danger' onClick={() => { removeItem(index) }} />
          </div>
        </Col>
      </Col>
    </Fragment>

  )
}

const RecurringInvoices = ({ addComplianceFlag, selectedService, refreshList }) => {

  const [block, setBlock] = useState(false)
  const [formModal, setFormModal] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const [data, setData] = useState('')

  const dispatch = useDispatch()
  // ** States
  const schema = yup.object().shape({
    createdBy: yup.string().default(userId),
    organization: yup.string().default(activeOrgId),
    serviceId: yup.string().required('Test'),
    name: yup.string().required(''),
    dueDate: yup.array().min(1),
    description: yup.string().default(''),
    importCode: yup.string().required(''),
    reminderOnActual: yup.array().min(1),
    recurringId: yup.number().default(1).required('Test'),
    forms: yup.array().of(
      yup.object().shape({
        name: yup.string().nullable(),
        filling_fees: yup.string().nullable(),
        late_fees_duration: yup.string().nullable(),
        penalty: yup.string().nullable()
      })
    )

  })

  const { handleSubmit, control, formState: { errors }, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const { fields, append, remove } = useFieldArray({ name: 'forms', control })

  const addItem = () => {

    append({})
  }

  const [clientOptions, setClientOptions] = useState([])

  const getClients = () => {
    setBlock(true)
    axios.post('/clients/dropdown', { status: 1 }).then(response => {
      const arr = response.data
      setBlock(false)
      setClientOptions(arr.clients)
    })
  }

  useEffect(async () => {
    await getClients()
    await addItem()
  }, [])


  const removeItem = (ind) => {
    remove(ind)
  }

  useEffect(() => {
    setFormModal(addComplianceFlag)
  }, [addComplianceFlag])

  const onSubmit = async data => {
    await dispatch(addCompliance(data))
    setFormModal(false)
    refreshList(true)
    remove()
  }

  const handleChange = (editorState) => {
    setData(editorState)
    control._formValues.recuurilngAlerts.forEach((data, i) => {
      setValue(`recuurilngAlerts[${i}].description`, draftToHtml(convertToRaw(editorState.getCurrentContent())))
    })
  }

  return (

    <Fragment>
      <Modal scrollable className='modal-dialog-centered modal-xl' isOpen={formModal} toggle={() => setFormModal(!formModal)}>
        <ModalBody>
          <Col className='d-flex justify-content-between'>
            <Col><h5>Add Recurring Email Alerts - <span className='text-primary'>{selectedService?.name}</span></h5></Col>
          </Col>
          <form onSubmit={handleSubmit(onSubmit)}>

            {Object.keys(errors).map((obj, k) => {
              return <FormFeedback key={k}> {obj}</FormFeedback>
            })}
            <div className='invoice-add'>
              <div id='message-editor'>
                <Editor
                  placeholder='Message'
                  toolbarClassName='rounded-0'
                  wrapperClassName='toolbar-bottom'
                  editorState={data}
                  onEditorStateChange={handleChange}
                  editorClassName='rounded-0 border-0'
                  toolbar={{
                    options: ['inline', 'textAlign'],
                    inline: {
                      inDropdown: false,
                      options: ['bold', 'italic', 'underline', 'strikethrough']
                    }
                  }}
                />
              </div>
              <div className='invoice-preview-card'>
                <UILoader blocking={block} loader={<Loader />}>
                  <CardBody className='invoice-padding invoice-product-details min-vh-100'>
                    {

                      fields.map((item, index) => {
                        return (
                          <div key={item.rowid} className='repeater-wrapper'>
                            <Row>
                              <Col className='d-lg-flex border p-2 position-relative rounded flex-wrap' sm='12'>
                                <InvoiceList item={item} index={index} clientOptions={clientOptions} setValue={setValue} setBlock={setBlock} errors={errors} control={control} removeItem={removeItem} />
                              </Col>
                            </Row>
                          </div>
                        )
                      })
                    }
                    <Button color='primary' outline size='sm' onClick={() => { addItem() }}>
                      <Plus size={15} /> Add New
                    </Button>
                  </CardBody>
                </UILoader>
              </div>
            </div>
          </form >
        </ModalBody>
        <ModalFooter>
          <Col className='d-flex justify-content-between'>
            <Col></Col>
            <Col className='d-flex justify-content-between'>
              <Col>
              </Col>
              <Col>
                <Button color='warning' onClick={() => { setFormModal(false); refreshList(false) }}>Cancel</Button>
                <Button color='primary' className='ms-50' type='submit' onClick={handleSubmit(onSubmit)}>Submit</Button>
              </Col>
            </Col>
          </Col>
        </ModalFooter>
      </Modal>
    </Fragment>

  )
}

export default RecurringInvoices
