// ** React Imports
import { Fragment, useEffect, useState } from 'react'
import toast from 'react-hot-toast'
import { useDropzone } from 'react-dropzone'
import axios from '@src/configs/axios/axiosConfig'
// ** Reactstrap Imports
import {
  Card,
  Input,
  Button,
  CardBody,
  Label,
  ListGroup,
  ListGroupItem,
  Col,
  Row,
  Progress,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from 'reactstrap'

// ** Third Party Components
import 'cleave.js/dist/addons/cleave-phone.us'
import { X, Paperclip, MoreVertical, Edit2, Trash } from 'react-feather'
import Avatar from '@src/@core/components/avatar'
import { getServiceTemplates, addTemplates, deleteTemplates, updateTemplates } from '../store/templates'
import { useDispatch, useSelector } from 'react-redux'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import moment from 'moment'
import { useParams } from 'react-router-dom'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
const MySwal = withReactContent(Swal)
// ** Images
const Templates = props => {
  // ** Hook
  const { tabId } = props
  const fileClassNames = {
    image: "ui-attachment--image",
    pdf: "ui-attachment--pdf",
    xlsx: "ui-attachment--excel",
    xls: "ui-attachment--excel",
    docx: "ui-attachment--docx",
    doc: "ui-attachment--docx"
  }

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const renderClient = row => {

    return (
      <Avatar
        initials
        className='me-1'
        color={'light-primary'}
        content={row.username?.charAt(0) || 'T'}
      />
    )

  }

  const dispatch = useDispatch()
  //** State */
  const { id } = useParams()
  //const [files, setFiles] = useState([])
  const [filesArr, setFilesArr] = useState([])
  const [data, setData] = useState([])
  const [attachmentIds, setAttachmentsId] = useState([])
  const [comment, setComment] = useState('')
  const [editcomment, setEditComment] = useState('')
  const [fileUploadProgress, setProgress] = useState(0)

  const store = useSelector(state => state.templates)

  const deletefn = (serid) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteTemplates({ serid, id }))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Service Template has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  const editNote = (k) => {
    const temp = data.map((obj, i) => {
      if (i === k) {
        setEditComment(obj.description)
        return { ...obj, editFlag: true }
      } else {
        return { ...obj, editFlag: false }
      }
    })
    setData(temp)
  }

  const dateformat = (val) => {

    return moment(val, 'x').format('YYYY, MMM-D h:m A')

  }

  const configUpload =
  {
    headers: {
      "Content-Type": "multipart/form-data"
    },
    onUploadProgress: data => {
      //Set the progress value to show the progress bar
      setProgress(Math.round((100 * data.loaded) / data.total))
    }
  }

  const uploadFiles = (files) => {

    const formData = new FormData()
    files.forEach((file, key) => {
      formData.append(`files[${key}]`, file)
    })
    formData.append('id', id)
    formData.append('organizationId', activeOrgId)
    formData.append('createdBy', userId)
    setBlock(true)

    axios.post('/google/servicefolders', formData, configUpload).then((res) => {
      setAttachmentsId([...attachmentIds, ...res.data.attachment_ids])
      //setFiles([])
      setBlock(false)
    }).catch((err) => { console.log(err); setBlock(false) })
  }

  const { getRootProps, getInputProps } = useDropzone({
    multiple: true,
    onDrop: async (files, rejectedFiles) => {

      const acceptedFiles = []
      files.forEach(obj => {
        obj.extension = obj.name.split('.').pop()
        acceptedFiles.push(obj)
      })
      if (rejectedFiles.length) {
        toast.error('You can only upload image Files!.')
      } else {
        await uploadFiles(acceptedFiles)
        //setFiles(acceptedFiles)
        setFilesArr([...filesArr, ...acceptedFiles])
      }
    }
  })

  const handleRemoveFile = (file, index) => {
    const uploadedFiles = filesArr
    const filtered = uploadedFiles.filter(i => i.name !== file.name)
    setAttachmentsId([...attachmentIds.splice(index, 1)])
    //setFiles(filtered)
    setFilesArr(filtered)
  }

  const saveNote = async (obj) => {
    obj.description = editcomment
    if (obj.description !== '') {
      await dispatch(updateTemplates(obj))
      toast.success('Comment Updated Successfully.', { position: 'bottom-right' })
      setEditComment('')
    }
  }

  useEffect(async () => {
    if (tabId === 'servicetemplate' && id !== undefined) {
      await dispatch(getServiceTemplates(id))
    }
  }, [tabId])

  useEffect(() => {
    setData(store.data || [])
  }, [store.data])

  const postComment = async () => {
    if (comment !== '' || attachmentIds.length > 0) {
      const data = {
        organizationId: activeOrgId,
        createdBy: userId,
        updatedBy: userId,
        description: comment,
        attachmentIds,
        serviceId: id
      }
      setBlock(true)
      await dispatch(addTemplates(data))
      toast.success('Comment Added Successfully.', { position: 'bottom-right' })
      //setFiles([])
      setAttachmentsId([])
      setFilesArr([])
      setProgress(0)
      setComment('')
      setBlock(false)
    }

  }

  const fileList = filesArr.map((file, index) => (
    <Col md={3} key={`${file.name}-${index}`} className={`d-flex align-items-center justify-content-between ui-attachment ${fileClassNames[file.extension] || fileClassNames['image']}`}>
      <div className='file-details d-flex align-items-center'>
        <div>
          <p className='file-name mb-0 text-overflow-ellipsis'>{file.name.split('.')[0].length > 15 ? `${file.name.split('.')[0].substr(0, 15)}` : file.name}</p>
        </div>
      </div>
      <X size={14} className='cursor-pointer'  onClick={() => handleRemoveFile(file, index)} />
    </Col>
  ))

  return (
    <Fragment>
      <UILoader blocking={block} loader={<Loader />}>
        <Card>
          <CardBody>

            <fieldset className='form-label-group mb-50'>
              <Label className='form-check-label' for={`add-comment-`}>
                Add Comment
              </Label>
              <Input id={`add-comment-`} type='textarea' rows='3' placeholder='Add Comment' value={comment} onInput={(e) => setComment(e.target.value)} />
            </fieldset>

            <input {...getInputProps()} />

            <Button type='button' color='primary' size='sm' {...getRootProps({  })} >
              <Paperclip size={14} className=''></Paperclip>
            </Button>

            <Button color='primary' className='ms-1' size='sm' onClick={() => postComment()}>
              Post Comment
            </Button>

            {fileUploadProgress > 0 && <Progress color={fileUploadProgress < 100 ? 'primary' : 'success'} value={fileUploadProgress} />}

            <Row className='d-flex p-1'>{fileList}</Row>

            {data.map((item, k) => {
              return (
                <div className='d-flex align-items-start my-1' key={k}>
                  {renderClient(item)}
                  <div className='profile-user-info w-100'>
                  <div className='d-flex align-items-center justify-content-between'>
                      <h6 className='mb-0'>{item.username}</h6>
                      <span className='d-flex text-body'>
                        <span className='text-sm text-body font-small-3 mt-25'>{dateformat(item.createdon)}</span>
                        {!item.editFlag &&
                          <UncontrolledDropdown>
                            <DropdownToggle tag='span'>
                              <MoreVertical size={16} />
                            </DropdownToggle>
                            <DropdownMenu end>
                              <DropdownItem>
                                <Col tag={'a'} href='/' onClick={(e) => { e.preventDefault(); editNote(k) }}>
                                  <Edit2 size={13} /> Edit
                                </Col>
                              </DropdownItem>
                              <DropdownItem>
                                <Col tag={'a'} href='/' onClick={(e) => { e.preventDefault(); deletefn(item.id) }}>
                                  <Trash size={13} /> Delete
                                </Col>
                              </DropdownItem>
                            </DropdownMenu>
                          </UncontrolledDropdown>
                        }
                      </span>
                    </div>
                    {!item.editFlag &&
                      <Row className='mt-1'>

                      <div className='white-space-wrap text-body '>
                        {item.description !== '' &&
                          < span className='rounded-3 bg-light-primary chat-bubble'>
                            {item.description.replace(/(\\n)/g, "\n")}
                          </span>
                        }
                        <Row>
                          {
                            item.files[0] !== null &&
                            item.files.map((file, k) => {
                              return (
                                <Col md={'3'} key={k} className='file-details d-flex align-items-center cursor-pointer'>
                                  <ListGroupItem className={`col-12 d-flex mb-1 align-items-center ui-attachment ${fileClassNames[file.extension] || fileClassNames['image']}`}>
                                    <a target='_blank' className='file-name text-overflow-ellipsis' href={`http://docs.google.com/fileview?id=${file.driveid}&hl=en&pid=explorer&efh=false&a=v&chrome=false&embedded=true`}>{file.originalfilename.split('.')[0].length > 15 ? `${file.originalfilename.split('.')[0].substr(0, 15)}.${file.extension}` : file.originalfilename}</a>
                                  </ListGroupItem>
                                </Col>
                              )
                            })
                          }
                        </Row>
                      </div>
                    </Row>
                    }
                    {item.editFlag &&
                      <Row className='mt-2'>
                        <Input type='textarea' roes='3' value={editcomment.replace(/(\\n)/g, "\n")} onInput={(e) => { setEditComment(e.target.value) }} />

                        <Col className='d-flex justify-content-end mt-1' >
                          <Button size='sm' color='warning' outline onClick={() => editNote(-1)}>Cancel</Button>
                          <Button size='sm' color='primary' className='ms-1' onClick={() => saveNote(item)} >Save</Button>
                        </Col>
                      </Row>
                    }
                  </div>
                </div>
              )
            })
            }
          </CardBody>
        </Card>
      </UILoader>
    </Fragment>
  )
}

export default Templates
