// ** React Imports
import { Link } from 'react-router-dom'
import { Can } from '@src/utility/context/Can.js'
// ** Custom Components
import Avatar from '@components/avatar'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
// ** Store & Actions
import { store } from '@store/store'
import { deleteCompliance } from '../store'

// ** Icons Imports
import { MoreVertical, Trash2, Archive, Eye } from 'react-feather'

// ** Reactstrap Imports
import { UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, Col } from 'reactstrap'
import moment from 'moment'

// ** Renders Client Columns
const renderClient = row => {

  return (
    <Avatar
      initials
      className='me-1'
      color={row.avatarColor || 'light-primary'}
      content={row.name || 'John Doe'}
    />
  )

}
const MySwal = withReactContent(Swal)
const recurringOptions = [
  '',
  'One time',
  'Monthly',
  'Quaterly',
  'Annually'
]

const deletefn = (id) => {

  return MySwal.fire({
    title: 'Are you sure?',
    text: "You won't be able to revert this!",
    icon: 'warning',
    showCancelButton: true,
    confirmButtonText: 'Yes, delete it!',
    customClass: {
      confirmButton: 'btn btn-primary',
      cancelButton: 'btn btn-outline-danger ms-1'
    },
    buttonsStyling: false
  }).then(async (result) => {
    if (result.value) {
      await store.dispatch(deleteCompliance(id))
      MySwal.fire({
        icon: 'success',
        title: 'Deleted!',
        text: 'Compliance has been deleted.',
        customClass: {
          confirmButton: 'btn btn-success'
        }
      })
    }
  })
}


const dateFormat = (value) => {
  const year = moment().format('y')
  if (value) {
    if (value.length === 13) {
      return moment(value, 'x').year(year).format("MMM DD, YYYY")
    } else {
      return moment.unix(value).year(year).format("MMM DD, YYYY")
    }
  } else {
    return ''
  }

}

export const columns = [
  {
    name: 'Filing Name',
    sortable: true,
    minWidth: '300px',
    sortField: 'name',
    selector: row => row.name,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>
        {renderClient(row)}
        <div className='d-flex flex-column'>
          <Link
            to={`/compliance/view/${row.id}`}
            className='user_name text-truncate text-body'
          >
            <span className='fw-bolder'>{row.name}</span>
          </Link>
        </div>
      </div>
    )
  },
  {
    name: 'No of Clients',
    sortable: true,
    minWidth: '172px',
    cell: row => <span>{row.noofclients} Client's</span>
  },
  {
    name: 'Upcoming Due Date',
    minWidth: '138px',
    sortable: true,
    cell: row => <span className='text-capitalize'>{dateFormat(row.duedate)}</span>
  },
  {
    name: 'Filing Period',
    minWidth: '230px',
    sortable: true,
    sortField: 'billing',
    selector: row => row.billing,
    cell: row => <span className='text-capitalize'>{recurringOptions[row.frequency]}</span>
  },
  {
    name: 'Actions',
    minWidth: '100px',
    cell: row => (
      <div className='d-flex column-action'>
        <Can I={'write'} a={'compliancealerts'}>
          <Col tag={Link} to={`/compliance/edit/${row.id}`} className='w-100' >
            <Eye size={14} className='me-50' />
          </Col>
        </Can>
        <UncontrolledDropdown>
          <DropdownToggle tag='div' className='btn btn-sm'>
            <MoreVertical size={14} className='cursor-pointer' />
          </DropdownToggle>
          <DropdownMenu>

            <Can I={'write'} a={'compliancealerts'}>
              <DropdownItem tag={Link} to={`/compliance/edit/${row.id}`} className='w-100' >
                <Archive size={14} className='me-50' />
                <span className='align-middle'>Edit</span>
              </DropdownItem>
            </Can>
            <Can I={'delete'} a={'compliancealerts'}>
              <DropdownItem
                className='w-100'
                onClick={e => {
                  e.preventDefault()
                  deletefn(row.id)
                }}
              >
                <Trash2 size={14} className='me-50' />
                <span className='align-middle'>Delete</span>
              </DropdownItem>
            </Can>
          </DropdownMenu>
        </UncontrolledDropdown>
      </div>
    )
  }
]
