// ** React Imports
import { useEffect, useState, Fragment } from 'react'
import { useForm, Controller, useFieldArray } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
// ** Third Party Components
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import { activeOrganizationid, activeOrganization, orgUserId } from '@src/helper/sassHelper'
const MySwal = withReactContent(Swal)
const activeOrg = activeOrganization()
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
// ** Reactstrap Imports
import { Card, CardBody, Row, Col, Label, FormFeedback, Modal, ModalBody, Button, ModalHeader, Input, CardText, ModalFooter } from 'reactstrap'
import { useDispatch } from 'react-redux'
import Select from 'react-select'
import axios from '@src/configs/axios/axiosConfig'
import { addInvoice } from '../store/index'
//import toast from 'react-hot-toast'
import classnames from 'classnames'

import Flatpickr from 'react-flatpickr'
import 'react-slidedown/lib/slidedown.css'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/base/pages/app-invoice.scss'
import { Plus, Trash2 } from 'react-feather'
import moment from 'moment'

const durationOptions = [
  { id: 1, name: "Monthly" },
  { id: 2, name: "Yearly" },
  { id: 3, name: "Quarterly" }
]

const colourStyles = {
  multiValue: (styles, { }) => {

    return {
      ...styles,
      backgroundColor: '#7367f0'
    }
  }
}
const InvoiceList = ({ index, taxGroupOptions, clientOptions, setValue, errors, control, removeItem }) => {

  const [dateList, setDateList] = useState([])

  useEffect(() => {
    setDateList(Array(30).fill().map((num, i) => { return { id: i + 1, name: i + 1 } }))
  }, [])

  const [duration, setDuration] = useState(1)
  const quarterlyDatesList = [
    {
      label: "Quarter (Apr - Jun)",
      invoiceDate: null,
      dueDate: null
    }, {
      label: "Quarter (Jul - Sep)",
      invoiceDate: null,
      dueDate: null
    }, {
      label: "Quarter (Oct - Dec)",
      invoiceDate: null,
      dueDate: null
    }, {
      label: "Quarter (Jan - Mar)",
      invoiceDate: null,
      dueDate: null
    }
  ]

  const handleQuarDateChange = (val, ind, fieldName, k) => {

    const res = moment(val[0]).startOf('d').valueOf()
    setValue(`recurringInvoices[${ind}].quarterlyDates[${k}].${fieldName}`, res)
  }

  const handleDateChange = (val, ind, fieldName) => {

    let res = ''
    const today = moment().format('D')
    if (duration === 2) {
      res = moment(val[0]).startOf('d').valueOf()
    } else if (duration === 1) {
      if (parseInt(today) <= val) {
        res = moment().set('D', val).startOf('d').valueOf()
      } else {
        res = moment().set('D', val).add(1, 'M').startOf('d').valueOf()
      }
    }

    setValue(`recurringInvoices[${ind}].${fieldName}`, res)

  }
  const handleDuration = (id) => {
    setDuration(id)
  }

  return (
    <Fragment>

      <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-3 col-sm-3'>
        <CardText className='col-title mb-md-50 mb-0'>Client</CardText>
        <Controller
          control={control}
          name={`recurringInvoices[${index}].clientId`}
          id={`recurringInvoices[${index}].clientId`}
          render={({ field, ref }) => (
            <Select
              {...field}
              inputRef={ref}
              className={classnames('react-select', { 'is-invalid': errors?.recurringInvoices })}
              {...field}
              classNamePrefix='select'
              options={clientOptions}
              placeholder='Select Client'
              value={clientOptions.find(c => { return c.id === field.value })}
              onChange={val => { field.onChange(val.id) }}
              getOptionLabel={(option) => option.name}
              getOptionValue={(option) => option.id}
              styles={colourStyles}
            />
          )}

        />
        {errors?.recurringInvoices && <FormFeedback className='text-danger'>{errors?.recurringInvoices[index]?.clientId?.message}</FormFeedback>}
      </Col>
      {activeOrg.isgstregistered &&
        <Fragment>
          <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-2'>
            <CardText className='col-title mb-md-50 mb-0'>SAC Code</CardText>
            <Controller
              control={control}
              name={`recurringInvoices[${index}].sacCode`}
              id={`recurringInvoices[${index}].sacCode`}
              render={({ field, value }) => (
                <Input
                  placeholder='SAC Code'
                  value={value}
                  type='text'
                  {...field}
                />
              )}
            />
            {errors?.recurringInvoices && <p className='text-danger'>{errors?.recurringInvoices[index]?.invoiceAmount?.message}</p>}
          </Col>
          <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-3 col-sm-3'>
            <CardText className='col-title mb-md-50 mb-0'>Tax Group</CardText>
            <Controller
              control={control}
              name={`recurringInvoices[${index}].taxGroupId`}
              id={`recurringInvoices[${index}].taxGroupId`}
              render={({ field, ref }) => (
                <Select
                  {...field}
                  inputRef={ref}
                  className={classnames('react-select', { 'is-invalid': errors?.recurringInvoices })}
                  {...field}
                  classNamePrefix='select'
                  options={taxGroupOptions}
                  placeholder='Select Tax Group'
                  value={taxGroupOptions.find(c => { return c.id === field.value })}
                  onChange={val => { field.onChange(val.id) }}
                  getOptionLabel={(option) => option.name}
                  getOptionValue={(option) => option.id}
                  styles={colourStyles}
                />
              )}

            />
            {errors?.recurringInvoices && <FormFeedback className='text-danger'>{errors?.recurringInvoices[index]?.clientId?.message}</FormFeedback>}
          </Col>
        </Fragment>
      }
      <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-2'>
        <CardText className='col-title mb-md-50 mb-0'>Amount</CardText>
        <Controller
          control={control}
          name={`recurringInvoices[${index}].invoiceAmount`}
          id={`recurringInvoices[${index}].invoiceAmount`}
          render={({ field, value }) => (
            <Input
              placeholder='Amount'
              value={value}
              type='text'
              {...field}
            />
          )}
        />
        {errors?.recurringInvoices && <p className='text-danger'>{errors?.recurringInvoices[index]?.invoiceAmount?.message}</p>}
      </Col>
      <Col className='mt-1 d-flex' md={12}>
        <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
          <Controller
            control={control}
            name={`recurringInvoices[${index}].recurringId`}
            id={`recurringInvoices[${index}].recurringId`}
            render={({ field, value, ref }) => (
              <Select
                {...field}
                inputRef={ref}
                className={classnames('react-select', { 'is-invalid': errors?.recurringInvoices })}
                {...field}
                classNamePrefix='select'
                options={durationOptions}
                placeholder='Select Frequency'
                value={durationOptions.find(c => { return c.id === value })}
                onChange={val => { field.onChange(val.id); handleDuration(val.id, index) }}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
              />
            )}

          />
          {errors?.recurringInvoices && <FormFeedback className='text-danger'>{errors?.recurringInvoices[index]?.recurringId?.message}</FormFeedback>}
        </Col>

        {
          duration === 1 &&
          <Fragment>
            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
              <Controller
                control={control}
                name={`recurringInvoices[${index}].invoiceDate`}
                id={`recurringInvoices[${index}].invoiceDate`}
                render={({ field, ref }) => (
                  <Select
                    {...field}
                    inputRef={ref}
                    className={classnames('react-select', { 'is-invalid': errors?.recurringInvoices })}
                    {...field}
                    classNamePrefix='select'
                    options={dateList}
                    placeholder='Select Date'
                    value={dateList.find(c => { return c.id === field.value })}
                    onChange={val => { field.onChange(val.id); handleDateChange(val.id, index, 'invoiceDate') }}
                    getOptionLabel={(option) => option.name}
                    getOptionValue={(option) => option.id}
                  />
                )}

              />
              {errors?.recurringInvoices && <FormFeedback className='text-danger'>{errors?.recurringInvoices[index]?.invoiceDate?.message}</FormFeedback>}
            </Col>

            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
              <Controller
                control={control}
                name={`recurringInvoices[${index}].dueDate`}
                id={`recurringInvoices[${index}].dueDate`}
                render={({ field, ref }) => (
                  <Select
                    {...field}
                    inputRef={ref}
                    className={classnames('react-select', { 'is-invalid': errors?.recurringInvoices })}
                    {...field}
                    classNamePrefix='select'
                    placeholder='Select Date'
                    options={dateList}
                    value={dateList.find(c => { return c.id === field.value })}
                    onChange={val => { field.onChange(val.id); handleDateChange(val.id, index, 'dueDate') }}
                    getOptionLabel={(option) => option.name}
                    getOptionValue={(option) => option.id}
                  />
                )}

              />
              {errors?.recurringInvoices && <FormFeedback className='text-danger'>{errors?.recurringInvoices[index]?.dueDate?.message}</FormFeedback>}
            </Col>
          </Fragment>
        }

        {
          duration === 2 &&
          <Fragment>
            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
              <Controller
                name={`recurringInvoices[${index}].invoiceDate`}
                id={`recurringInvoices[${index}].invoiceDate`}
                control={control}
                rules={{ required: true }}
                options={{ dateFormat: "d-m-Y" }}
                render={({ field }) => (
                  <Flatpickr
                    min={moment().add(1, 'D').format('Y-m-d')}
                    value={field.value}
                    placeholder='Select Date'
                    onChange={(date, dateStr) => { field.onChange(dateStr); handleDateChange(date, index, 'invoiceDate') }}
                    options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                    className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.invoiceDate })} />
                )}
              />
              {errors?.recurringInvoices && <FormFeedback className='text-danger'>{errors?.recurringInvoices[index]?.invoiceDate?.message}</FormFeedback>}
            </Col>

            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
              <Controller
                onChange={date => setDate(date)}
                name={`recurringInvoices[${index}].dueDate`}
                id={`recurringInvoices[${index}].dueDate`}
                control={control}
                rules={{ required: true }}
                options={{ dateFormat: "d-m-Y" }}
                render={({ field }) => (
                  <Flatpickr
                    min={moment().add(1, 'D').format('Y-m-d')}
                    value={field.value}
                    placeholder='Select Date'
                    onChange={(date, dateStr) => { field.onChange(dateStr); handleDateChange(date, index, 'dueDate') }}
                    options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                    className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.dueDate })} />
                )}
              />

              {errors?.recurringInvoices && <FormFeedback className='text-danger'>{errors?.recurringInvoices[index]?.dueDate?.message}</FormFeedback>}
            </Col>

          </Fragment>
        }{
          duration === 3 &&
          <Col md={6} >
            {
              quarterlyDatesList.map((item, k) => {
                return (
                  <Col className='d-flex mb-1' key={k}>
                    <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-4 col-sm-3'>
                      <Controller
                        name={`recurringInvoices[${index}].quarterlyDates[${k}].invoiceDate`}
                        id={`recurringInvoices[${index}].quarterlyDates[${k}].invoiceDate`}
                        control={control}
                        rules={{ required: true }}
                        options={{ dateFormat: "d-m-Y" }}
                        render={({ field }) => (
                          <Flatpickr
                            value={field.value}
                            placeholder='Select Date'
                            onChange={(date, dateStr) => { field.onChange(dateStr); handleQuarDateChange(date, index, 'invoiceDate', k) }}
                            options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                            className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.invoiceDate })} />
                        )}
                      />
                      {errors?.recurringInvoices && <FormFeedback className='text-danger'>{errors?.recurringInvoices[index]?.invoiceDate?.message}</FormFeedback>}
                    </Col>

                    <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-4 col-sm-3'>
                      <Controller
                        onChange={date => setDate(date)}
                        name={`recurringInvoices[${index}].quarterlyDates[${k}].dueDate`}
                        id={`recurringInvoices[${index}].quarterlyDates[${k}].dueDate`}
                        control={control}
                        rules={{ required: true }}
                        options={{ dateFormat: "d-m-Y" }}
                        render={({ field }) => (
                          <Flatpickr
                            value={field.value}
                            placeholder='Select Date'
                            onChange={(date, dateStr) => { field.onChange(dateStr); handleQuarDateChange(date, index, 'dueDate', k) }}
                            options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                            className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.dueDate })} />
                        )}
                      />

                      {errors?.recurringInvoices && <FormFeedback className='text-danger'>{errors?.recurringInvoices[index]?.dueDate?.message}</FormFeedback>}
                    </Col>
                    <Col>{item.label}</Col>
                  </Col>
                )
              })
            }
          </Col>
        }
        <Col lg={1}>
          <div className='d-lg-flex justify-content-center invoice-product-actions pt-1 px-25'>
            <Trash2 size={16} className='text-danger' onClick={() => { removeItem(index) }} />
          </div>
        </Col>
      </Col>
    </Fragment>

  )
}

const RecurringInvoices = ({ id, addInvoiceFlag, selectedService, refreshList }) => {

  const [block, setBlock] = useState(false)
  const [formModal, setFormModal] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const dispatch = useDispatch()
  // ** States
  const schema = yup.object().shape({
    recurringInvoices: yup.array().of(
      yup.object().shape({
        clientId: yup.string().required("Please Select Client"),
        createdBy: yup.string().default(userId),
        organizationId: yup.string().default(activeOrgId),
        serviceId: yup.string().default(id),
        isgstregistered: yup.boolean().default(activeOrg.isgstregistered),
        recurringId: yup.number().default(1).required("Please Select Frequency"),
        taxGroupId: yup.string().when("isgstregistered", { is: (isgstregistered) => isgstregistered, then: yup.string().required("Please Select Tax Group") }).default(''),
        sacCode: yup.string().default(""),
        invoiceDate: yup.string().when("recurringId", { is: (recurringId) => (recurringId === 1 || recurringId === 2), then: yup.string().required("Please Enter Invoice Date") }),
        dueDate: yup.string().when("recurringId", { is: (recurringId) => (recurringId === 1 || recurringId === 2), then: yup.string().required("Please Enter Due Date") }),
        invoiceAmount: yup.string().default('0.00'),
        quarterlyDates: yup.array().of(
          yup.object().shape({
            invoiceDate: yup.string().when("recurringId", { is: (recurringId) => parseInt(recurringId) === 3, then: yup.string().required("Please Enter Invoice Date") }),
            dueDate: yup.string().when("recurringId", { is: (recurringId) => parseInt(recurringId) === 3, then: yup.string().required("Please Enter Due Date") })
          })
        )
      })
    ).min(1, "Require Atleast one Item to create task.")

  })

  const { handleSubmit, formState: { errors }, control, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const { fields, append, remove } = useFieldArray({ name: 'recurringInvoices', control, keyName: "rowid" })

  const addItem = () => {

    append({})
  }

  const [clientOptions, setClientOptions] = useState([])
  const [taxGroupOptions, setTaxGroupOptions] = useState([])
  const getTaxGroups = () => {
    axios.post('/taxgroups/dropdown').then(response => {
      const arr = response.data
      setTaxGroupOptions(arr.taxgroups)
    })
  }
  const getClients = () => {
    setBlock(true)
    axios.post('/clients/dropdown', { status: 1 }).then(response => {
      const arr = response.data
      setBlock(false)
      setClientOptions(arr.clients)
    })
  }

  useEffect(async () => {
    await getClients()
    await getTaxGroups()
    await addItem()
  }, [])


  const removeItem = (ind) => {
    remove(ind)
  }

  useEffect(() => {
    setFormModal(addInvoiceFlag)
  }, [addInvoiceFlag])

  const onSubmit = async data => {
    await dispatch(addInvoice(data))
    setFormModal(false)
    refreshList(true)
    remove()
  }
  return (

    <Fragment>
      <Modal scrollable className='modal-dialog-centered modal-xl' isOpen={formModal} toggle={() => setFormModal(!formModal)}>
        <ModalBody>
          <Col className='d-flex justify-content-between'>
            <Col><h5>Add Recurring Invoice - <span className='text-primary'>{selectedService?.name}</span></h5></Col>
          </Col>
          <form onSubmit={handleSubmit(onSubmit)}>

            {Object.keys(errors).map((obj, k) => {
              console.log(errors)
              return (
                <FormFeedback key={k}> {obj.message}
                  {Object.keys(obj).map((oj, i) => {
                    <FormFeedback key={i}> {oj.message}</FormFeedback>
                  })
                  }
                </FormFeedback>
              )

            })}
            <div className='invoice-add'>
              <div className='invoice-preview-card'>
                <UILoader blocking={block} loader={<Loader />}>
                  <CardBody className='invoice-padding invoice-product-details min-vh-100'>
                    {

                      fields.map((item, index) => {
                        return (
                          <div key={item.rowid} className='repeater-wrapper'>
                            <Row>
                              <Col className='d-lg-flex border p-2 position-relative rounded flex-wrap' sm='12'>
                                <InvoiceList item={item} index={index} taxGroupOptions={taxGroupOptions} clientOptions={clientOptions} setValue={setValue} setBlock={setBlock} errors={errors} control={control} removeItem={removeItem} />
                              </Col>
                            </Row>
                          </div>
                        )
                      })
                    }
                    <Button color='primary' outline size='sm' onClick={() => { addItem() }}>
                      <Plus size={15} /> Add New
                    </Button>
                  </CardBody>
                </UILoader>
              </div>
            </div>

          </form >

        </ModalBody>
        <ModalFooter>
          <Col className='d-flex justify-content-between'>
            <Col></Col>
            <Col className='d-flex justify-content-between'>
              <Col>
              </Col>
              <Col>
                <Button color='warning' onClick={() => { setFormModal(false); refreshList(false) }}>Cancel</Button>
                <Button color='primary' className='ms-50' type='submit' onClick={handleSubmit(onSubmit)}>Submit</Button>
              </Col>
            </Col>
          </Col>
        </ModalFooter>
      </Modal>
    </Fragment>

  )
}

export default RecurringInvoices
