// ** React Imports
import { useEffect, useState, Fragment } from 'react'
import { useForm, Controller } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
// ** Third Party Components
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import { activeOrganizationid, activeOrganization, orgUserId } from '@src/helper/sassHelper'
const MySwal = withReactContent(Swal)
const activeOrg = activeOrganization()
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
// ** Reactstrap Imports
import { Card, CardBody, Row, Col, Label, FormFeedback, Modal, ModalBody, Button, ModalHeader, Input, CardText, ModalFooter } from 'reactstrap'
import { useDispatch } from 'react-redux'
import Select from 'react-select'
import axios from '@src/configs/axios/axiosConfig'
import { updateInvoice } from '../store/index'
//import toast from 'react-hot-toast'
import classnames from 'classnames'

import Flatpickr from 'react-flatpickr'
import 'react-slidedown/lib/slidedown.css'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/base/pages/app-invoice.scss'
import moment from 'moment'

const durationOptions = [
  { id: 1, name: "Monthly" },
  { id: 2, name: "Yearly" },
  { id: 3, name: "Quarterly" }
]

const colourStyles = {
  multiValue: (styles, { }) => {

    return {
      ...styles,
      backgroundColor: '#7367f0'
    }
  }
}

const RecurringInvoices = ({ id, editModal, selInvoice, selectedService, closeEdit, refreshInvoiceList }) => {

  const [block, setBlock] = useState(false)
  const [formModal, setFormModal] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const [dateList, setDateList] = useState([])

  useEffect(() => {
    setDateList(Array(30).fill().map((num, i) => { return { id: i + 1, name: i + 1 } }))
  }, [])

  const [duration, setDuration] = useState(1)
  const quarterlyDatesList = [
    {
      label: "Quarter (Apr - Jun)",
      invoiceDate: null,
      dueDate: null
    }, {
      label: "Quarter (Jul - Sep)",
      invoiceDate: null,
      dueDate: null
    }, {
      label: "Quarter (Oct - Dec)",
      invoiceDate: null,
      dueDate: null
    }, {
      label: "Quarter (Jan - Mar)",
      invoiceDate: null,
      dueDate: null
    }
  ]

  const handleDuration = (id) => {
    setDuration(id)
  }

  const dispatch = useDispatch()
  // ** States
  const schema = yup.object().shape({
    clientId: yup.string().required("Please Select Client"),
    updatedBy: yup.string().default(userId),
    id: yup.string().default(selInvoice?.id),
    organizationId: yup.string().default(activeOrgId),
    serviceId: yup.string().default(id),
    recurringId: yup.number().default(2).required("Please Select Frequency"),
    isgstregistered: yup.boolean().default(activeOrg.isgstregistered),
    taxGroupId: yup.string().when("isgstregistered", { is: (isgstregistered) => isgstregistered, then: yup.string().required("Please Select Tax Group") }),
    sacCode: yup.string().default(""),
    invoiceDate: yup.string().when("recurringId", { is: (recurringId) => (recurringId === 1 || recurringId === 2), then: yup.string().required("Please Enter Invocie Date") }),
    dueDate: yup.string().when("recurringId", { is: (recurringId) => (recurringId === 1 || recurringId === 2), then: yup.string().required("Please Enter Due Date") }),
    invoiceAmount: yup.string().default('0.00'),
    quarterlyDates: yup.array().of(
      yup.object().shape({
        invoiceDate: yup.string().when("recurringId", { is: (recurringId) => recurringId === 3, then: yup.string().required("Please Enter Invoice Date") }),
        dueDate: yup.string().when("recurringId", { is: (recurringId) => recurringId === 3, then: yup.string().required("Please Enter Due Date") })
      }).when("recurringId", { is: (recurringId) => (recurringId === 3), then: yup.array().min(4, "Please Enter Dates") })
    ).nullable()
  })

  const { handleSubmit, formState: { errors }, control, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const [clientOptions, setClientOptions] = useState([])
  const [taxGroupOptions, setTaxGroupOptions] = useState([])
  const getTaxGroups = () => {
    axios.post('/taxgroups/dropdown').then(response => {
      const arr = response.data
      setTaxGroupOptions(arr.taxgroups)
    })
  }
  const getClients = () => {
    setBlock(true)
    axios.post('/clients/dropdown', { status: 1 }).then(response => {
      const arr = response.data
      setBlock(false)
      setClientOptions(arr.clients)
    })
  }

  useEffect(async () => {
    await getClients()
    await getTaxGroups()

  }, [])

  useEffect(() => {
    setFormModal(editModal)
  }, [editModal])

  useEffect(() => {
    setValue('clientId', selInvoice?.clientid)
    setValue('id', selInvoice?.id)
    setValue('serviceId', selInvoice?.serviceid)
    setValue('recurringId', parseInt(selInvoice?.recurringid))
    setDuration(parseInt(selInvoice?.recurringid))
    setValue('taxGroupId', selInvoice?.taxrate)
    setValue('sacCode', selInvoice?.saccode)

    if (parseInt(selInvoice?.recurringid) === 1) {
      const date1 = moment(selInvoice?.invoicedate, 'x').format('D')
      const date2 = moment(selInvoice?.duedate, 'x').format('D')
      setValue('invoiceDate', parseInt(date1))
      setValue('dueDate', parseInt(date2))
    } else {
      const date1 = String(selInvoice?.invoicedate / 1000)
      const date2 = String(selInvoice?.duedate / 1000)
      setValue('invoiceDate', date1)
      setValue('dueDate', date2)
    }

    setValue('invoiceAmount', selInvoice?.amount)

    let temp = []
    if (selInvoice?.quarterlydates?.length > 0) {
      temp = selInvoice?.quarterlydates.map((oj) => {
        const t = JSON.parse(oj)
        t['invoiceDate'] = String((t?.invoiceDate ?? t?.startDate) / 1000)
        t['dueDate'] = String((t?.dueDate ?? t?.endDate) / 1000)
        return t
      }
      )
    }
    setValue('quarterlyDates', temp)

  }, [selInvoice])

  const handleQuarDateChange = (val, fieldName, k) => {

    const res = moment(val[0]).startOf('d').valueOf()
    setValue(`quarterlyDates[${k}].${fieldName}`, res)
  }

  const handleDateChange = (val, fieldName, flg = false) => {

    let res = ''
    const today = moment().format('D')
    if (duration === 2) {
      res = moment(val[0]).startOf('d').valueOf()
    } else if (duration === 1) {
      if (parseInt(today) <= val) {
        res = moment().set('D', val).startOf('d').valueOf()
      } else {
        res = moment().set('D', val).add(1, 'M').startOf('d').valueOf()
      }
    }

    setValue(`${fieldName}`, res)
    if (flg) return res
  }

  const onSubmit = async data => {
    if (duration === 3) {
      data['quarterlyDates'] = data?.quarterlyDates.map((oj) => {
        const t = oj
        if (t?.startDate) {
          t['invoiceDate'] = String(t?.startDate).length === 10 ? (t?.startDate * 1000) : t?.startDate
          t['dueDate'] = String(t?.endDate).length === 10 ? (t?.endDate * 1000) : t?.endDate
        } else {
          t['invoiceDate'] = String(t?.invoiceDate).length === 10 ? (t?.invoiceDate * 1000) : t?.invoiceDate
          t['dueDate'] = String(t?.dueDate).length === 10 ? (t?.dueDate * 1000) : t?.dueDate
        }

        return t
      })
    } else {
      if (data.invoiceDate.length !== 13 && data.invoiceDate.length !== 10) {
        data.invoiceDate = handleDateChange(parseInt(data.invoiceDate), 'invoiceDate', true)
        data.dueDate = handleDateChange(parseInt(data.dueDate), 'dueDate', true)
      }
    }

    await dispatch(updateInvoice(data))
    setFormModal(false)
    refreshInvoiceList(true)
  }

  const cancelEdit = () => {
    setFormModal(false)
    closeEdit(false)
    refreshInvoiceList(false)
  }
  return (

    <Fragment>
      <Modal scrollable className='modal-dialog-centered modal-xl' isOpen={formModal} toggle={() => setFormModal(!formModal)}>
        <ModalBody>
          <Col className='d-flex justify-content-between'>
            <Col><h5>Add Recurring Invoice - <span className='text-primary'>{selectedService?.name}</span></h5></Col>
          </Col>
          <form onSubmit={handleSubmit(onSubmit)}>

            {Object.keys(errors).map((obj, k) => {
              console.log(errors)
              return (
                <FormFeedback key={k}> {obj.message}
                  {Object.keys(obj).map((oj, i) => {
                    <FormFeedback key={i}> {oj.message}</FormFeedback>
                  })
                  }
                </FormFeedback>
              )

            })}
            <div className='invoice-add'>
              <div className='invoice-preview-card'>
                <UILoader blocking={block} loader={<Loader />}>
                  <CardBody className='invoice-padding invoice-product-details'>
                    <div className='repeater-wrapper'>
                      <Row>
                        <Col className='d-lg-flex border p-2 position-relative rounded flex-wrap' sm='12'>
                          <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-3 col-sm-3'>
                            <CardText className='col-title mb-md-50 mb-0'>Client</CardText>
                            <Controller
                              control={control}
                              name={`clientId`}
                              id={`clientId`}
                              render={({ field, ref }) => (
                                <Select
                                  inputRef={ref}
                                  className={classnames('react-select', { 'is-invalid': errors?.clientId })}
                                  {...field}
                                  classNamePrefix='select'
                                  options={clientOptions}
                                  placeholder='Select Client'
                                  value={clientOptions.find(c => { return c.id === field.value })}
                                  onChange={val => { field.onChange(val.id) }}
                                  getOptionLabel={(option) => option.name}
                                  getOptionValue={(option) => option.id}
                                  styles={colourStyles}
                                />
                              )}

                            />
                            {errors?.clientId && <FormFeedback className='text-danger'>{errors?.clientId?.message}</FormFeedback>}
                          </Col>
                          {activeOrg.isgstregistered &&
                            <Fragment>
                              <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-2'>
                                <CardText className='col-title mb-md-50 mb-0'>SAC Code</CardText>
                                <Controller
                                  control={control}
                                  name={`sacCode`}
                                  id={`sacCode`}
                                  render={({ field, value }) => (
                                    <Input
                                      placeholder='SAC Code'
                                      value={value}
                                      type='text'
                                      {...field}
                                    />
                                  )}
                                />
                                {errors?.sacCode && <p className='text-danger'>{errors?.sacCode?.message}</p>}
                              </Col>
                              <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-3 col-sm-3'>
                                <CardText className='col-title mb-md-50 mb-0'>Tax Group</CardText>
                                <Controller
                                  control={control}
                                  name={`taxGroupId`}
                                  id={`taxGroupId`}
                                  render={({ field, ref }) => (
                                    <Select
                                      inputRef={ref}
                                      className={classnames('react-select', { 'is-invalid': errors?.taxGroupId })}
                                      {...field}
                                      classNamePrefix='select'
                                      options={taxGroupOptions}
                                      placeholder='Select Tax Group'
                                      value={taxGroupOptions.find(c => { return c.id === field.value })}
                                      onChange={val => { field.onChange(val.id) }}
                                      getOptionLabel={(option) => option.name}
                                      getOptionValue={(option) => option.id}
                                      styles={colourStyles}
                                    />
                                  )}

                                />
                                {errors?.taxGroupId && <FormFeedback className='text-danger'>{errors?.taxGroupId?.message}</FormFeedback>}
                              </Col>
                            </Fragment>
                          }
                          <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-2'>
                            <CardText className='col-title mb-md-50 mb-0'>Amount</CardText>
                            <Controller
                              control={control}
                              name={`invoiceAmount`}
                              id={`invoiceAmount`}
                              render={({ field, value }) => (
                                <Input
                                  placeholder='Amount'
                                  value={value}
                                  type='text'
                                  {...field}
                                />
                              )}
                            />
                            {errors?.invoiceAmount && <p className='text-danger'>{errors?.invoiceAmount?.message}</p>}
                          </Col>
                          <Col className='mt-1 d-flex' md={12}>
                            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
                              <Controller
                                control={control}
                                name={`recurringId`}
                                id={`recurringId`}
                                render={({ field, ref }) => (
                                  <Select
                                    inputRef={ref}
                                    className={classnames('react-select', { 'is-invalid': errors?.recu })}
                                    {...field}
                                    classNamePrefix='select'
                                    options={durationOptions}
                                    placeholder='Select Frequency'
                                    value={durationOptions.find(c => { return c.id === field.value })}
                                    onChange={val => { field.onChange(val.id); handleDuration(val.id) }}
                                    getOptionLabel={(option) => option.name}
                                    getOptionValue={(option) => option.id}
                                  />
                                )}

                              />
                              {errors?.recu && <FormFeedback className='text-danger'>{errors?.recurringId?.message}</FormFeedback>}
                            </Col>

                            {
                              duration === 1 &&
                              <Fragment>
                                <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
                                  <Controller
                                    control={control}
                                    name={`invoiceDate`}
                                    id={`invoiceDate`}
                                    render={({ field, ref }) => (
                                      <Select
                                        {...field}
                                        inputRef={ref}
                                        className={classnames('react-select', { 'is-invalid': errors?.invoiceDate })}
                                        classNamePrefix='select'
                                        options={dateList}
                                        placeholder='Select Date'
                                        value={dateList.find(c => { return c.id === field.value })}
                                        onChange={val => { field.onChange(val.id); handleDateChange(val.id, 'invoiceDate') }}
                                        getOptionLabel={(option) => option.name}
                                        getOptionValue={(option) => option.id}
                                      />
                                    )}

                                  />
                                  {errors?.invoiceDate && <FormFeedback className='text-danger'>{errors?.invoiceDate?.message}</FormFeedback>}
                                </Col>

                                <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
                                  <Controller
                                    control={control}
                                    name={`dueDate`}
                                    id={`dueDate`}
                                    render={({ field, ref }) => (
                                      <Select
                                        {...field}
                                        inputRef={ref}
                                        className={classnames('react-select', { 'is-invalid': errors?.dueDate })}
                                        classNamePrefix='select'
                                        placeholder='Select Date'
                                        options={dateList}
                                        value={dateList.find(c => { return c.id === field.value })}
                                        onChange={val => { field.onChange(val.id); handleDateChange(val.id, 'dueDate') }}
                                        getOptionLabel={(option) => option.name}
                                        getOptionValue={(option) => option.id}
                                      />
                                    )}

                                  />
                                  {errors?.dueDate && <FormFeedback className='text-danger'>{errors?.dueDate?.message}</FormFeedback>}
                                </Col>
                              </Fragment>
                            }

                            {
                              duration === 2 &&
                              <Fragment>
                                <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
                                  <Controller
                                    name={`invoiceDate`}
                                    id={`invoiceDate`}
                                    control={control}
                                    rules={{ required: true }}
                                    options={{ dateFormat: "d-m-Y" }}
                                    render={({ field }) => (
                                      <Flatpickr
                                        value={field.value}
                                        placeholder='Select Date'
                                        onChange={(date, dateStr) => { field.onChange(dateStr); handleDateChange(date, 'invoiceDate') }}
                                        options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                                        className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.invoiceDate })} />
                                    )}
                                  />
                                  {errors && <FormFeedback className='text-danger'>{errors?.invoiceDate?.message}</FormFeedback>}
                                </Col>

                                <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
                                  <Controller
                                    onChange={date => setDate(date)}
                                    name={`dueDate`}
                                    id={`dueDate`}
                                    control={control}
                                    rules={{ required: true }}
                                    options={{ dateFormat: "d-m-Y" }}
                                    render={({ field }) => (
                                      <Flatpickr
                                        value={field.value}
                                        placeholder='Select Date'
                                        onChange={(date, dateStr) => { field.onChange(dateStr); handleDateChange(date, 'dueDate') }}
                                        options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                                        className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.dueDate })} />
                                    )}
                                  />

                                  {errors && <FormFeedback className='text-danger'>{errors?.dueDate?.message}</FormFeedback>}
                                </Col>

                              </Fragment>
                            }{
                              duration === 3 &&
                              <Col md={6} >
                                {
                                  quarterlyDatesList.map((item, k) => {
                                    return (
                                      <Col className='d-flex mb-1' key={k}>
                                        <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-4 col-sm-3'>
                                          <Controller
                                            name={`quarterlyDates[${k}].invoiceDate`}
                                            id={`quarterlyDates[${k}].invoiceDate`}
                                            control={control}
                                            render={({ field }) => (
                                              <Flatpickr
                                                value={field.value}
                                                placeholder='Select Date'
                                                onChange={(date, dateStr) => { field.onChange(dateStr); handleQuarDateChange(date, 'invoiceDate', k) }}
                                                options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                                                className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.invoiceDate })} />
                                            )}
                                          />
                                          {errors && <FormFeedback className='text-danger'>{errors?.invoiceDate?.message}</FormFeedback>}
                                        </Col>

                                        <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-4 col-sm-3'>
                                          <Controller
                                            control={control}
                                            name={`quarterlyDates[${k}].dueDate`}
                                            id={`quarterlyDates[${k}].dueDate`}
                                            render={({ field }) => (
                                              <Flatpickr
                                                value={field.value}
                                                placeholder='Select Date'
                                                onChange={(date, dateStr) => { field.onChange(dateStr); handleQuarDateChange(date, 'dueDate', k) }}
                                                options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                                                className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.dueDate })} />
                                            )}
                                          />

                                          {errors && <FormFeedback className='text-danger'>{errors?.dueDate?.message}</FormFeedback>}
                                        </Col>
                                        <Col>{item.label}</Col>
                                      </Col>
                                    )
                                  })
                                }
                              </Col>
                            }
                          </Col>
                        </Col>
                      </Row>
                    </div>
                  </CardBody>
                </UILoader>
              </div>
            </div>
            <ModalFooter>
              <Col className='d-flex justify-content-between'>
                <Col></Col>
                <Col className='d-flex justify-content-between'>
                  <Col>
                  </Col>
                  <Col>
                    <Button color='warning' onClick={() => { cancelEdit() }}>Cancel</Button>
                    <Button color='primary' className='ms-50' type='submit'>Submit</Button>
                  </Col>
                </Col>
              </Col>
            </ModalFooter>
          </form >

        </ModalBody>
      </Modal>
    </Fragment >

  )
}

export default RecurringInvoices
