// ** React Imports
import { Fragment, useState, useEffect } from 'react'
import { Link, useParams } from 'react-router-dom'
// ** Invoice List Sidebar
import { Can } from '@src/utility/context/Can.js'

// ** Store & Actions
import { deleteInvoice, getData } from '../store'
import { useDispatch, useSelector } from 'react-redux'
import axios from '@src/configs/axios/axiosConfig'
// ** Third Party Components
import Select from 'react-select'
import ReactPaginate from 'react-paginate'
import { Edit, MoreVertical, Trash2 } from 'react-feather'

// ** Utils
import AddInvoice from '@src/views/apps/recurring-invoice/add/AddCard.js'
import EditInvoice from '@src/views/apps/recurring-invoice/edit/EditCard.js'

import { selectThemeColors } from '@utils'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
// ** Reactstrap Imports
import {
  Row,
  Col,
  Card,
  Input,
  Label,
  Button,
  CardBody,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Table
} from 'reactstrap'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
const frequencyOptions = ['', 'Monthly', 'Yearly', 'Quarterly']
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import { orgUserId } from '@src/helper/sassHelper'
import moment from 'moment'
const userId = orgUserId()
const MySwal = withReactContent(Swal)
// ** Table Header
const CustomHeader = ({ handlePerPage, rowsPerPage, handleFilter, searchTerm, selectedService, refreshInvoiceList, selectedIds }) => {
  // ** Converts table to CSV
  const [addInvoiceFlag, setAddInvoiceFlag] = useState(false)
  const { id } = useParams()
  const refreshList = (val) => {
    if (val) {
      refreshInvoiceList(true)
    }
    setAddInvoiceFlag(false)
  }

  const deleteInvoice = () => {

    axios.post('/recurringinvoices/deletebulk', { selectedIds, updatedBy: userId })
      .then(() => {
        refreshInvoiceList(true)
      })
      .catch(() => { })
  }

  const deletefn = () => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await deleteInvoice(selectedIds)
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Task has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  return (
    <div className='invoice-list-table-header ps-1 mt-2 mb-75'>
      <Row>

        {
          addInvoiceFlag &&
          <AddInvoice id={id} addInvoiceFlag={addInvoiceFlag} selectedService={selectedService} refreshList={refreshList} />
        }
        <Col xl='6' md='6' sm='6' className='d-flex align-items-center p-0'>
          <div className='d-flex align-items-center'>
            <label htmlFor='rows-per-page'>Show</label>
            <Input
              className='mx-50'
              type='select'
              id='rows-per-page'
              value={rowsPerPage}
              onChange={handlePerPage}
              style={{ width: '5rem' }}
            >
              <option value='10'>10</option>
              <option value='25'>25</option>
              <option value='50'>50</option>
            </Input>
            <label htmlFor='rows-per-page'>Entries</label>
          </div>
          {selectedIds.length > 0 &&
            <Col onClick={() => { deletefn() }}>
              <Trash2 size={18} className='text-danger cursor-pointer ms-1' />
            </Col>
          }
        </Col>
        <Col md='6' sm='6'
          xl='6'
          className='d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'
        >
          <div className='d-flex align-items-center mb-sm-0 mb-1 me-1'>
            <label className='mb-0' htmlFor='search-invoice'>
              Search:
            </label>
            <Input
              id='search-invoice'
              className='ms-50 w-100'
              type='text'
              value={searchTerm}
              onChange={e => handleFilter(e.target.value)}
            />
          </div>

          <div className='d-flex align-items-center table-header-actions'>
            <Can I={'write'} a={'recurring-task'}>
              <Button className='add-new-user' color='primary' onClick={() => { setAddInvoiceFlag(true) }}>
                Add
              </Button>
            </Can>
          </div>
        </Col>
      </Row>
    </div>
  )
}

const InvoiceList = ({ selectedService, tabId }) => {
  // ** Store Vars
  const store = useSelector(state => state.recurringinvoice)
  const [block, setBlock] = useState(false)
  const dispatch = useDispatch()
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const { id } = useParams()
  // ** States
  const [sort, setSort] = useState('desc')
  const [searchTerm, setSearchTerm] = useState('')
  const [currentPage, setCurrentPage] = useState(1)
  const [sortColumn, setSortColumn] = useState('createdon')
  const [rowsPerPage, setRowsPerPage] = useState(50)
  const [currentClient, setCurrentClient] = useState({ id: '', name: 'Select Client' })
  const currentService = id

  const [selectedIds, setSelectedIds] = useState([])
  const handleCheckBox = (e, id) => {
    if (e.target.checked) {
      setSelectedIds([...selectedIds, id])
    } else {
      const temp = [...selectedIds]
      const ind = temp.indexOf(id)
      temp.splice(ind, 1)
      setSelectedIds(temp)
    }

  }

  // ** Get data on mount
  useEffect(async () => {
    if (tabId === 'recurring_invoices') {
      setBlock(true)
      await dispatch(
        getData({
          sort,
          sortColumn,
          q: searchTerm,
          client: currentClient.id,
          perPage: rowsPerPage,
          page: currentPage,
          recurringFlag: true,
          service: currentService || ''
        })

      )
      setBlock(false)
    }
  }, [dispatch, tabId])


  const refreshInvoiceList = async (val) => {

    if (!val) return
    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        client: currentClient.id,
        perPage: rowsPerPage,
        page: currentPage,
        recurringFlag: true,
        service: currentService || ''
      })

    )
    setBlock(false)
  }

  const [clientOptions, setClientOptions] = useState([])
  const [editModal, setEditModal] = useState(false)
  const [selInvoice, setSelInvoice] = useState(null)

  const openEditModal = (inv) => {
    setEditModal(true)
    setSelInvoice(inv)
  }

  const getClients = () => {
    axios.post('/clients/dropdown', { status: 1 }).then(response => {
      const arr = response.data.clients
      arr.unshift({ id: '', name: 'Select All' })
      setClientOptions(arr)
    })
  }


  useEffect(() => {
    if (tabId === 'recurring_invoices') {
      getClients()
      setEditModal(false)
    }
  }, [tabId])


  // ** Function in get data on page change
  const handlePagination = async page => {
    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        client: currentClient.id,
        perPage: rowsPerPage,
        page: page.selected + 1,
        service: currentService || '',
        recurringFlag: true
      })
    )
    setCurrentPage(page.selected + 1)
    setBlock(false)
  }

  // ** Function in get data on rows per page
  const handlePerPage = async e => {
    const value = parseInt(e.currentTarget.value)
    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        client: currentClient.id,
        service: currentService || '',
        perPage: rowsPerPage,
        page: currentPage,
        recurringFlag: true
      })
    )
    setRowsPerPage(value)
    setBlock(false)
  }

  // ** Function in get data on search query change
  const handleFilter = async val => {
    setSearchTerm(val)
    setBlock(true)
    await dispatch(
      getData({
        sort,
        q: val,
        sortColumn,
        perPage: rowsPerPage,
        page: currentPage,
        client: currentClient.id,
        service: currentService || '',
        recurringFlag: true
      })
    )
    setBlock(false)
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
      />
    )
  }

  const closeEdit = (val) => {
    setEditModal(val)
    setSelInvoice(null)
  }

  const deletefn = (id) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteInvoice(id))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Task has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      client: currentClient.id,
      service: currentService || '',
      recurringFlag: true,
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.invoiceData.length > 0) {
      return store.invoiceData
    } else if (store.invoiceData.length === 0 && isFiltered) {
      return []
    }
  }

  const handleSort = async (column, sortDirection) => {
    setSort(sortDirection)
    setSortColumn(column.sortField)
    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        page: currentPage,
        perPage: rowsPerPage,
        client: currentClient.id,
        recurringFlag: true,
        service: currentService || ''
      })
    )
    setBlock(false)
  }
  return (
    <Fragment>
      <Card className='my-0 pb-0'>
        <CardBody className='my-0 p-0'>
          <Row>
            <Col md='3'>
              <Label for='role-select'>Client</Label>
              <Select
                label={'name'}
                isClearable={false}
                value={currentClient}
                options={clientOptions}
                className='react-select'
                classNamePrefix='select'
                theme={selectThemeColors}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
                onChange={async data => {
                  setCurrentClient(data)
                  setBlock(true)
                  await dispatch(
                    getData({
                      sort,
                      sortColumn,
                      q: searchTerm,
                      client: data.id,
                      page: currentPage,
                      perPage: rowsPerPage,

                      recurringFlag: true,
                      service: currentService || ''
                    })
                  )
                  setBlock(false)
                }}
              />
            </Col>
          </Row>
        </CardBody>
      </Card>
      <EditInvoice id={id} selInvoice={selInvoice} editModal={editModal} selectedService={selectedService} closeEdit={closeEdit} refreshInvoiceList={refreshInvoiceList} />
      <UILoader blocking={block} loader={<Loader />}>
        <Card className='overflow-hidden'>
          <CustomHeader
            store={store}
            searchTerm={searchTerm}
            rowsPerPage={rowsPerPage}
            handleFilter={handleFilter}
            handlePerPage={handlePerPage}
            selectedService={selectedService}
            refreshInvoiceList={refreshInvoiceList}
            selectedIds={selectedIds}
          />
          <div className='react-dataTable'>
            <Table>
              <thead>
                <tr>
                  <th></th>
                  <th onClick={() => handleSort()}>Client</th>
                  <th>Frequency</th>
                  <th>Amount</th>
                  <th>Date</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {dataToRender().length > 0 && dataToRender().map((row, k) => {
                  return (<tr key={k}>
                    <td>
                      <Input type='checkbox' onChange={(e) => { handleCheckBox(e, row.id) }} />
                    </td>
                    <td>{row.contactname}</td>
                    <td>{
                      frequencyOptions[row.recurringid]
                    }</td>
                    <td>{row.amount}</td>
                    <td>{moment(row.invoicedate, 'x').format('MMM DD, Y')}</td>
                    <td>
                      <div className='column-action d-flex align-items-center'>
                        <Can I={'write'} a={'recurring-task'}>
                          <Col lg={4}
                            onClick={() => openEditModal(row)} >
                            <Edit
                              className='cursor-pointer ms-1 mt-0' size={16} />
                          </Col>
                        </Can>
                        <UncontrolledDropdown>
                          <DropdownToggle tag='div' className='btn btn-sm'>
                            <MoreVertical size={14} className='cursor-pointer' />
                          </DropdownToggle>
                          <DropdownMenu>
                            <Can I={'delete'} a={'recurring-task'}>
                              <DropdownItem
                                tag='a'
                                href='/'
                                className='w-100'
                                onClick={e => {
                                  e.preventDefault()
                                  deletefn(row.id)
                                }}
                              >
                                <Trash2 size={14} className='me-50' />
                                <span className='align-middle'>Delete</span>
                              </DropdownItem>
                            </Can>
                          </DropdownMenu>
                        </UncontrolledDropdown>
                      </div>
                    </td>
                  </tr>)
                })
                }
              </tbody>
            </Table>

            <CustomPagination />
          </div>
        </Card>
      </UILoader>
    </Fragment>
  )
}

export default InvoiceList
