// ** React Imports
import { Fragment, useEffect } from 'react'
import { Link } from 'react-router-dom'
// ** Reactstrap Imports
import { Card, CardBody, Button, Badge, Row, Col } from 'reactstrap'
// ** Styles
import axios from '@src/configs/axios/axiosConfig'
import '@styles/react/libs/react-select/_react-select.scss'
import { activeOrganization } from '@src/helper/sassHelper'
import { Edit, Plus, X } from 'react-feather'
const activrOrg = activeOrganization()
const statusColors = [
  '',
  'light-primary',
  'light-warning'
]

const statusArr = [
  '',
  "Active",
  "Inactive"

]

const UserInfoCard = ({ selectedService }) => {

  const getList = () => {

    axios.post('/serviceforms/list', { serviceId: selectedService?.id })
      .then((res) => {
        setFormOptions(res.data.serviceforms)
        if (selectedService?.formslist) {
          const temp = []
          res.data.serviceforms?.map((obj) => {
            if (selectedService?.formslist.indexOf(obj.id) > -1) {
              temp.push({ name: obj.name, id: obj.id })
            }
          })
          setFormSelected(temp)
        }
      })
      .catch(() => { })
  }

  useEffect(async () => {
    await getList()
  }, [])

  return (
    <Fragment>
      <Card>
        <CardBody>
          <Row className='d-flex justify-content-between border-bottom align-items-center pb-1'>
            <Col>
              <h5>{selectedService.name}</h5>
            </Col>
            <Col className='d-flex'>
              <Col className='col-md-11'></Col>
              <Col tag={Link} to={`/services/edit/${selectedService.id}`} >
                <Edit className='cursor-pointer' size={18} />
              </Col>
              <Col tag={Link} to={`/services/list`}>
                <X size={18} className='ms-1 cursor-pointer' />
              </Col>
            </Col>
          </Row>
          <div className='info-container mt-1'>
            {selectedService !== null ? (

              <Row>
                <Col className='border border-bottom-0 border-top-0 border-start-0'>
                  <ul className='list-unstyled'>
                    <li className='mb-75 row'>
                      <span className='fw-bolder col-6'>Fee:</span>
                      <span className='col-6 text-end'>{selectedService.sellingprice}</span>
                    </li>
                    {activrOrg.countryshortname === 'IN' &&
                      <li className='mb-75 row'>
                        <span className='fw-bolder col-6'>SAC Code:</span>
                        <span className='col-6 text-end'>{selectedService.saccode}</span>
                      </li>
                    }
                    {activrOrg.isgstregistered &&
                      <Fragment>
                        {
                          activrOrg.countryshortname === 'IN' &&
                          <li className='mb-75 row'>
                            <span className='fw-bolder col-6'>Tax Rate:</span>
                            <span className='col-6 text-end'>{selectedService.taxgroupsname}</span>
                          </li>
                        }
                        {(!selectedService.istaxapplicable && activrOrg.countryshortname === 'IN') &&
                          <li className='mb-75 row'>
                            <span className='fw-bolder col-6'>Exemption Reason:</span>
                            <span className='col-6 text-end'>{selectedService.exemptionreasonsname || '-'}</span>
                          </li>
                        }
                      </Fragment>
                    }
                    <li className='mb-75 row'>
                      <span className='fw-bolder col-4'>Forms:</span>
                      <div className='d-flex'>
                        {selectedService.forms?.map((form, k) => {
                          return (
                            <Badge className='me-50 mt-50' key={k} color='primary'>{form.name}</Badge>
                          )
                        })}
                      </div>
                    </li>
                  </ul>
                </Col>
                <Col>
                  <ul className='list-unstyled'>
                    <li className='mb-75 row'>
                      <span className='fw-bolder col-6'>Turn Around Time(TAT):</span>
                      <span className='col-6 text-end'>{selectedService.tat || 0}</span>
                    </li>
                    <li className='mb-75 row'>
                      <span className='fw-bolder col-6'>Status:</span>
                      <span className='col-6 text-end'>
                        <Badge className='text-capitalize' color={statusColors[selectedService.status]}>
                          {statusArr[selectedService.status]}
                        </Badge>
                      </span>
                    </li>
                    <li className='mb-75 row'>
                      <span className='fw-bolder col-6'>Description:</span>
                      <span className='col-6 text-end'>{selectedService.description}</span>
                    </li>
                  </ul>
                </Col>
              </Row>

            ) : null}
          </div>
        </CardBody>
      </Card>

    </Fragment >
  )
}

export default UserInfoCard
