// ** React Imports
import { useEffect, useState, Fragment } from 'react'
import { useForm, Controller } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
// ** Third Party Components
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
const MySwal = withReactContent(Swal)
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
// ** Reactstrap Imports
import { Card, CardBody, Row, Col, Label, FormFeedback, Modal, ModalBody, Button, ModalHeader, Input, CardText, ModalFooter } from 'reactstrap'
import { useDispatch } from 'react-redux'
import Select from 'react-select'
import axios from '@src/configs/axios/axiosConfig'
import { updateTask } from '../store/index'
//import toast from 'react-hot-toast'
import classnames from 'classnames'

import Flatpickr from 'react-flatpickr'
import 'react-slidedown/lib/slidedown.css'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/base/pages/app-invoice.scss'
import { Plus, Trash2 } from 'react-feather'
import moment from 'moment'

const priorityOptions = [
  { id: 1, name: "Low" },
  { id: 2, name: "Medium" },
  { id: 3, name: "High" }
]

const durationOptions = [
  { id: 1, name: "Monthly" },
  { id: 2, name: "Yearly" },
  { id: 3, name: "Quarterly" },
  { id: 4, name: "Daily" },
  { id: 5, name: "Weekly" }
]

const colourStyles = {
  multiValue: (styles, { }) => {

    return {
      ...styles,
      backgroundColor: '#7367f0'
    }
  }
}
const RecurringTasks = (props) => {
  const [block, setBlock] = useState(false)
  const [formModal, setFormModal] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const dispatch = useDispatch()
  const { id, editTaskFlag, selTask, selectedService, setEditClose, refreshTaskList } = props

  // ** States
  const schema = yup.object().shape({

    clientId: yup.string().required("Please Select Client"),
    updatedBy: yup.string().default(userId),
    id: yup.string().default(selTask?.id),
    organizationId: yup.string().default(activeOrgId),
    serviceId: yup.string().default(id),
    invoiceFlag: yup.boolean().default(false),
    recurringId: yup.number().default(1).required("Please Select Frequency"),
    priority: yup.number().default(1).required("Please Select Priority"),
    startDate: yup.string().when("recurringId", { is: (recurringId) => (recurringId === 1 || recurringId === 2 || recurringId === 5), then: yup.string().required("Please Enter Start Date") }),
    endDate: yup.string().when("recurringId", { is: (recurringId) => (recurringId === 1 || recurringId === 2 || recurringId === 5), then: yup.string().required("Please Enter End Date") }),
    assigneeId: yup.array().min(1, 'Atleast require ine Selection'),
    reviewerId: yup.array(),
    invoiceAmount: yup.string().default('0.00').nullable(),
    quarterlyDates: yup.array().of(
      yup.object().shape({
        startDate: yup.string().when("duration", { is: (duration) => parseInt(duration) === 3, then: yup.string().required("Please Enter Start Date") }),
        endDate: yup.string().when("duration", { is: (duration) => parseInt(duration) === 3, then: yup.string().required("Please Enter End Date") })
      })
    )

  })

  const { handleSubmit, formState: { errors }, control, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })
  const [userOptions, setUserOptions] = useState([])
  const [clientOptions, setClientOptions] = useState([])

  const [dateList, setDateList] = useState([])
  const dayList = [{ id: 0, name: 'Sunday' }, { id: 1, name: 'Monday' }, { id: 2, name: 'Tuesday' }, { id: 3, name: 'Wednesday  ' }, { id: 4, name: 'Thursday' }, { id: 5, name: 'Friday' }, { id: 6, name: 'Saturday' }]
  useEffect(() => {
    setDateList(Array(30).fill().map((num, i) => { return { id: i + 1, name: i + 1 } }))
  }, [])

  const [assigneeUserOptions, setAssigneeUserOptions] = useState([])
  const [duration, setDuration] = useState(1)
  const [reviewerUserOptions, setReviewerUserOptions] = useState([])
  const [assigneeDefaultValue, setAssigneeDefaultValue] = useState()
  const [reviewerDefaultValue, setReviewerDefaultValue] = useState()
  const quarterlyDatesList = [
    {
      label: "Quarter (Apr - Jun)",
      startDate: null,
      endDate: null
    }, {
      label: "Quarter (Jul - Sep)",
      startDate: null,
      endDate: null
    }, {
      label: "Quarter (Oct - Dec)",
      startDate: null,
      endDate: null
    }, {
      label: "Quarter (Jan - Mar)",
      startDate: null,
      endDate: null
    }
  ]
  useEffect(() => {
    setAssigneeUserOptions(userOptions)
    setReviewerUserOptions(userOptions)
  }, [userOptions])

  const handleQuarDateChange = (val, fieldName, k) => {

    const res = moment(val[0]).startOf('d').valueOf()
    setValue(`quarterlyDates[${k}].${fieldName}`, res)
  }

  const handleDateChange = (val, fieldName, flg) => {

    let res = ''
    const today = moment().format('D')
    if (duration === 2) {
      res = moment(val[0]).startOf('d').valueOf()
    } else if (duration === 5) {
      const today_day = moment().format('d')
      if (parseInt(today_day) >= val) {
        res = moment().set(val, 'd').add(1, 'w').startOf('d').valueOf()
      } else {
        res = moment().set(val, 'd').startOf('d').valueOf()
      }
    } else if (duration === 1) {
      if (parseInt(today) <= val) {
        res = moment().set('D', val).startOf('d').valueOf()
      } else {
        res = moment().set('D', val).add(1, 'M').startOf('d').valueOf()
      }
    }

    setValue(`${fieldName}`, res)
    if (flg) return res
  }

  const handleAssigneeChange = (e, flag = false) => {
    setAssigneeDefaultValue(e)
    let tempArr = []
    if (!flag) {
      tempArr = Array.isArray(e) ? e.map(x => x.id) : []
    } else {
      tempArr = Array.isArray(e) ? e.map(x => x.userid) : []
    }
    let reviewerOptions
    if (tempArr.length > 0) {
      reviewerOptions = userOptions.filter(({ id: id1 }) => !tempArr.some(id2 => id2 === id1))
    } else { reviewerOptions = userOptions }

    setReviewerUserOptions(reviewerOptions)
    setValue("assigneeId", tempArr)
  }

  const handleReviwerChange = (e, flag = false) => {
    setReviewerDefaultValue(e)
    let tempArr = []
    if (!flag) {
      tempArr = Array.isArray(e) ? e.map(x => x.id) : []
    } else {
      tempArr = Array.isArray(e) ? e.map(x => x.userid) : []
    }
    let assigneeOptions
    if (tempArr.length > 0) {
      assigneeOptions = userOptions.filter(({ id: id1 }) => !tempArr.some(id2 => id2 === id1))
    } else { assigneeOptions = userOptions }

    setAssigneeUserOptions(assigneeOptions)
    setValue("reviewerId", tempArr)
  }


  const handleDuration = (id) => {
    setDuration(id)
    setValue(`startDate`, null)
    setValue(`endDate`, null)
  }

  const getClients = () => {
    setBlock(true)
    axios.post('/clients/dropdown', { status: 1 }).then(response => {
      const arr = response.data
      setBlock(false)
      setClientOptions(arr.clients)
    })
  }

  const getOrganizationUsers = async () => {
    axios.post('/organizationusers/dropdown').then(response => {
      const arr = response.data
      setUserOptions(arr.organizationusers)
      setReviewerUserOptions(arr.organizationusers)
      setAssigneeUserOptions(arr.organizationusers)
    }).catch((err) => {
      console.log(err)
    })
  }

  useEffect(() => {
    if (selTask) {
      setValue('clientId', selTask?.clientid)
      setValue('id', selTask?.id)
      setValue('invoiceFlag', selTask?.invoiceflag)
      setValue('recurringId', parseInt(selTask?.recurringid))
      setValue('priority', parseInt(selTask?.priority))
      setValue('invoiceAmount', selTask?.taskprice)
      setDuration(parseInt(selTask?.recurringid))
      let stdate, enddate
      if (parseInt(selTask?.recurringid) === 1) {
        stdate = moment(selTask?.nextrecurringstartdate, 'x').format('D')
        enddate = moment(selTask?.nextrecurringenddate, 'x').format('D')
        setValue('startDate', parseInt(stdate))
        setValue('endDate', parseInt(enddate))
      } else if (parseInt(selTask?.recurringid) === 5) {
        stdate = moment(selTask?.nextrecurringstartdate, 'x').format('d')
        enddate = moment(selTask?.nextrecurringenddate, 'x').format('d')
        setValue('startDate', parseInt(stdate))
        setValue('endDate', parseInt(enddate))
      } else {
        stdate = String(selTask?.nextrecurringstartdate / 1000)
        enddate = String(selTask?.nextrecurringenddate / 1000)
        setValue('startDate', stdate)
        setValue('endDate', enddate)
        let temp = []
        if (selTask?.quarterlydates?.length > 0) {
          temp = selTask?.quarterlydates.map((oj) => {
            const t = JSON.parse(oj)
            t['startDate'] = String(t?.startDate / 1000)
            t['endDate'] = String(t?.endDate / 1000)
            return t
          }
          )
        }
        setValue('quarterlyDates', temp)
      }

      handleAssigneeChange(selTask?.assignee, true)
      handleReviwerChange(selTask?.reviewer, true)
    }
  }, [selTask])

  useEffect(async () => {
    await getOrganizationUsers()
    await getClients()
  }, [])

  useEffect(() => {
    setFormModal(editTaskFlag)
  }, [editTaskFlag])

  const onSubmit = async data => {

    const deletedids = []
    selTask?.assignee.forEach((obj) => {
      if (!data.assigneeId.includes(obj.id)) {
        deletedids.push(obj.id)
      }
    })

    selTask?.reviewer.forEach((obj) => {
      if (!data.reviewerId.includes(obj.id)) {
        deletedids.push(obj.id)
      }
    })
    data['deletedIds'] = deletedids
    setBlock(true)
    if (duration === 3) {
      data['quarterlyDates'] = data?.quarterlyDates.map((oj) => {
        const t = oj
        t['startDate'] = String(t?.startDate).length === 10 ? (t?.startDate * 1000) : t?.startDate
        t['endDate'] = String(t?.endDate).length === 10 ? (t?.endDate * 1000) : t?.endDate
        return t
      })

    } else {
      if (data.startDate.length !== 13 && data.startDate.length !== 10) {
        data.startDate = handleDateChange(parseInt(data.startDate), 'startDate', true)
        data.endDate = handleDateChange(parseInt(data.endDate), 'endDate', true)
      }
    }

    await dispatch(updateTask(data))
    setFormModal(false)
    setEditClose(false)
    refreshTaskList(true)
    setBlock(false)
  }
  const customStyles = {
    control: base => ({
      ...base,
      minHeight: '30px',
      height: '30px'
    })
  }
  return (

    <Fragment>
      <Modal scrollable className='modal-dialog-centered modal-xl' isOpen={formModal} toggle={() => setFormModal(!formModal)}>
        <ModalBody>
          <Col className='d-flex justify-content-between'>
            <Col><h5>Add Recurring Task - <span className='text-primary'>{selectedService?.name}</span></h5></Col>
          </Col>
          <form onSubmit={handleSubmit(onSubmit)}>

            {Object.keys(errors).map((obj, k) => {
              return <FormFeedback key={k}> {obj}</FormFeedback>
            })}
            <div className='invoice-add'>
              <div className='invoice-preview-card'>
                <UILoader blocking={block} loader={<Loader />}>
                  <CardBody className='invoice-padding invoice-product-details'>

                    <div className='repeater-wrapper'>
                      <Row>
                        <Col className='d-lg-flex border p-2 position-relative rounded flex-wrap' sm='12'>
                          <Fragment>

                            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
                              <CardText className='col-title mb-md-50 mb-0'>Client</CardText>
                              <Controller
                                control={control}
                                name={`clientId`}
                                id={`clientId`}
                                render={({ field, ref }) => (
                                  <Select
                                    {...field}
                                    inputRef={ref}
                                    className={classnames('react-select', { 'is-invalid': errors?.clientId })}
                                    classNamePrefix='select'
                                    options={clientOptions}
                                    placeholder='Select Client'
                                    value={clientOptions.find(c => { return c.id === field.value })}
                                    onChange={val => { field.onChange(val.id) }}
                                    getOptionLabel={(option) => option.name}
                                    getOptionValue={(option) => option.id}
                                    styles={colourStyles}
                                  />
                                )}

                              />
                              {errors?.clientId && <FormFeedback className='text-danger'>{errors?.clientId?.message}</FormFeedback>}
                            </Col>

                            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-1 col-sm-3'>
                              <CardText className='col-title mb-md-50 mb-0'>Priority</CardText>
                              <Controller
                                control={control}
                                name={`priority`}
                                id={`priority`}
                                render={({ field, ref }) => (
                                  <Select
                                    {...field}
                                    inputRef={ref}
                                    className={classnames('react-select', { 'is-invalid': errors?.priority })}
                                    classNamePrefix='select'
                                    placeholder='Priority'
                                    options={priorityOptions}
                                    value={priorityOptions.find(c => { return c.id === field.value })}
                                    onChange={val => field.onChange(val.id)}
                                    getOptionLabel={(option) => option.name}
                                    getOptionValue={(option) => option.id}
                                  />
                                )}

                              />
                              {errors?.priority && <FormFeedback className='text-danger'>{errors?.priority?.message}</FormFeedback>}
                            </Col>
                            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-3 col-sm-4'>
                              <CardText className='col-title mb-md-50 mb-0'>Assignee</CardText>
                              <Controller
                                control={control}
                                name="assignee"
                                id="assignee"
                                render={({ field, ref }) => (
                                  <Select
                                    inputRef={ref}
                                    className={classnames('react-select', { 'is-invalid': errors?.assignee })}
                                    {...field}
                                    classNamePrefix='select'
                                    options={assigneeUserOptions}
                                    isMulti={true}
                                    value={assigneeDefaultValue} // set selected values
                                    placeholder='Select Assignee'
                                    onChange={(e) => { handleAssigneeChange(e) }}
                                    getOptionLabel={(option) => option.name}
                                    getOptionValue={(option) => option.id}
                                    styles={colourStyles}
                                  />
                                )}

                              />
                              {errors?.assignee && <FormFeedback className='text-danger'>{errors?.assignee?.message}</FormFeedback>}
                            </Col>
                            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-3 col-sm-4'>
                              <CardText className='col-title mb-md-50 mb-0'>Reviewer</CardText>
                              <Controller
                                control={control}
                                name="reviewer"
                                id="reviewer"
                                render={({ field, ref }) => (
                                  <Select
                                    inputRef={ref}
                                    className={classnames('react-select', { 'is-invalid': errors?.reviewer })}
                                    {...field}
                                    classNamePrefix='select'
                                    options={reviewerUserOptions}
                                    isMulti={true}
                                    value={reviewerDefaultValue} // set selected values
                                    placeholder='Select Reviewer'
                                    onChange={(e) => { handleReviwerChange(e) }}
                                    getOptionLabel={(option) => option.name}
                                    getOptionValue={(option) => option.id}
                                    styles={colourStyles}
                                  />
                                )}
                              />
                              {errors?.reviewer && <FormFeedback className='text-danger'>{errors?.reviewer?.message}</FormFeedback>}
                            </Col>
                            <Col>
                              <CardText className='col-title mb-md-50 mb-0'>Recurring Invoice</CardText>
                              <div className='form-switch form-check-primary'>
                                <Controller
                                  control={control}
                                  name={`invoiceFlag`}
                                  id={`invoiceFlag`}
                                  render={({ field }) => (
                                    <Input
                                      defaultChecked={selTask?.invoiceflag}
                                      type='switch'
                                      {...field}
                                      name={`invoiceFlag`} />
                                  )}
                                />
                              </div>
                            </Col>
                            <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-1 col-sm-2'>
                              <CardText className='col-title mb-md-50 mb-0'>Amount</CardText>
                              <Controller
                                control={control}
                                name={`invoiceAmount`}
                                id={`invoiceAmount`}
                                render={({ field, value }) => (
                                  <Input
                                    placeholder='Amount'
                                    value={value}
                                    type='text'
                                    {...field}
                                  />
                                )}
                              />
                              {errors?.invoiceAmount && <p className='text-danger'>{errors?.invoiceAmount?.message}</p>}
                            </Col>
                            <Col className='mt-1 d-flex' md={12}>
                              <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
                                <Controller
                                  control={control}
                                  name={`recurringId`}
                                  id={`recurringId`}
                                  render={({ field, ref }) => (
                                    <Select
                                      {...field}
                                      inputRef={ref}
                                      className={classnames('react-select', { 'is-invalid': errors?.recurringId })}
                                      classNamePrefix='select'
                                      options={durationOptions}
                                      placeholder='Select Frequency'
                                      value={durationOptions.find(c => { return c.id === field.value })}
                                      onChange={val => { field.onChange(val.id); handleDuration(val.id) }}
                                      getOptionLabel={(option) => option.name}
                                      getOptionValue={(option) => option.id}
                                      styles={customStyles}
                                    />
                                  )}

                                />
                                {errors?.recurringId && <FormFeedback className='text-danger'>{errors?.recurringId?.message}</FormFeedback>}
                              </Col>

                              {
                                duration === 1 &&
                                <Fragment>
                                  <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
                                    <Controller
                                      control={control}
                                      name={`startDate`}
                                      id={`startDate`}
                                      render={({ field, ref }) => (
                                        <Select
                                          {...field}
                                          inputRef={ref}
                                          className={classnames('react-select', { 'is-invalid': errors?.startDate })}
                                          classNamePrefix='select'
                                          options={dateList}
                                          placeholder='Select Date'
                                          value={dateList.find(c => { return c.id === field.value })}
                                          onChange={val => { field.onChange(val.id); handleDateChange(val.id, 'startDate') }}
                                          getOptionLabel={(option) => option.name}
                                          getOptionValue={(option) => option.id}
                                          styles={customStyles}
                                        />
                                      )}

                                    />
                                    {errors?.startDate && <FormFeedback className='text-danger'>{errors?.startDate?.message}</FormFeedback>}
                                  </Col>

                                  <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
                                    <Controller
                                      control={control}
                                      name={`endDate`}
                                      id={`endDate`}
                                      render={({ field, ref }) => (
                                        <Select
                                          inputRef={ref}
                                          className={classnames('react-select', { 'is-invalid': errors?.endDate })}
                                          {...field}
                                          classNamePrefix='select'
                                          placeholder='Select Date'
                                          options={dateList}
                                          value={dateList.find(c => { return c.id === field.value })}
                                          onChange={val => { field.onChange(val.id); handleDateChange(val.id, 'endDate') }}
                                          getOptionLabel={(option) => option.name}
                                          getOptionValue={(option) => option.id}
                                        />
                                      )}

                                    />
                                    {errors?.endDate && <FormFeedback className='text-danger'>{errors?.endDate?.message}</FormFeedback>}
                                  </Col>
                                </Fragment>
                              }
                              {
                                duration === 5 &&
                                <Fragment>
                                  <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
                                    <Controller
                                      control={control}
                                      name={`startDate`}
                                      id={`startDate`}
                                      render={({ field, ref }) => (
                                        <Select
                                          {...field}
                                          inputRef={ref}
                                          className={classnames('react-select', { 'is-invalid': errors?.startDate })}
                                          classNamePrefix='select'
                                          options={dayList}
                                          placeholder='Select Date'
                                          value={dayList.find(c => { return c.id === field.value })}
                                          onChange={val => { field.onChange(val.id); handleDateChange(val.id, 'startDate') }}
                                          getOptionLabel={(option) => option.name}
                                          getOptionValue={(option) => option.id}
                                        />
                                      )}

                                    />
                                    {errors?.startDate && <FormFeedback className='text-danger'>{errors?.startDate?.message}</FormFeedback>}
                                  </Col>
                                  <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
                                    <Controller
                                      control={control}
                                      name={`endDate`}
                                      id={`endDate`}
                                      render={({ field, ref }) => (
                                        <Select
                                          {...field}
                                          inputRef={ref}
                                          className={classnames('react-select', { 'is-invalid': errors?.endDate })}
                                          classNamePrefix='select'
                                          placeholder='Select Date'
                                          options={dayList}
                                          value={dayList.find(c => { return c.id === field.value })}
                                          onChange={val => { field.onChange(val.id); handleDateChange(val.id, 'endDate') }}
                                          getOptionLabel={(option) => option.name}
                                          getOptionValue={(option) => option.id}
                                        />
                                      )}

                                    />
                                    {errors?.endDate && <FormFeedback className='text-danger'>{errors?.endDate?.message}</FormFeedback>}
                                  </Col>
                                </Fragment>
                              }
                              {
                                duration === 2 &&
                                <Fragment>
                                  <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
                                    <Controller
                                      name={`startDate`}
                                      id={`startDate`}
                                      control={control}
                                      rules={{ required: true }}
                                      options={{ dateFormat: "d-m-Y" }}
                                      render={({ field }) => (
                                        <Flatpickr
                                          min={moment().add(1, 'D').format('Y-m-d')}
                                          value={field.value}
                                          placeholder='Select Date'
                                          onChange={(date, dateStr) => { field.onChange(dateStr); handleDateChange(date, 'startDate') }}
                                          options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                                          className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.startDate })} />
                                      )}
                                    />
                                    {errors?.startDate && <FormFeedback className='text-danger'>{errors?.startDate?.message}</FormFeedback>}
                                  </Col>

                                  <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-2 col-sm-3'>
                                    <Controller
                                      onChange={date => setDate(date)}
                                      name={`endDate`}
                                      id={`endDate`}
                                      control={control}
                                      rules={{ required: true }}
                                      options={{ dateFormat: "d-m-Y" }}
                                      render={({ field }) => (
                                        <Flatpickr
                                          min={moment().add(1, 'D').format('Y-m-d')}
                                          value={field.value}
                                          placeholder='Select Date'
                                          onChange={(date, dateStr) => { field.onChange(dateStr); handleDateChange(date, 'endDate') }}
                                          options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                                          className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.endDate })} />
                                      )}
                                    />

                                    {errors?.endDate && <FormFeedback className='text-danger'>{errors?.endDate?.message}</FormFeedback>}
                                  </Col>

                                </Fragment>
                              }{
                                duration === 3 &&
                                <Col md={6} >
                                  {
                                    quarterlyDatesList.map((item, k) => {
                                      return (
                                        <Col className='d-flex mb-1' key={k}>
                                          <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-4 col-sm-3'>
                                            <Controller
                                              name={`quarterlyDates[${k}].startDate`}
                                              id={`quarterlyDates[${k}].startDate`}
                                              control={control}
                                              rules={{ required: true }}
                                              options={{ dateFormat: "d-m-Y" }}
                                              render={({ field }) => (
                                                <Flatpickr
                                                  value={field.value}
                                                  placeholder='Select Date'
                                                  onChange={(date, dateStr) => { field.onChange(dateStr); handleQuarDateChange(date, 'startDate', k) }}
                                                  options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                                                  className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.startDate })} />
                                              )}
                                            />
                                            {errors?.startDate && <FormFeedback className='text-danger'>{errors?.startDate?.message}</FormFeedback>}
                                          </Col>

                                          <Col className='me-50 mb-lg-0 mb-2 mt-lg-0 mt-2 col-lg-4 col-sm-3'>
                                            <Controller
                                              onChange={date => setDate(date)}
                                              name={`quarterlyDates[${k}].endDate`}
                                              id={`quarterlyDates[${k}].endDate`}
                                              control={control}
                                              rules={{ required: true }}
                                              options={{ dateFormat: "d-m-Y" }}
                                              render={({ field }) => (
                                                <Flatpickr
                                                  value={field.value}
                                                  placeholder='Select Date'
                                                  onChange={(date, dateStr) => { field.onChange(dateStr); handleQuarDateChange(date, 'endDate', k) }}
                                                  options={{ altInput: true, altFormat: "F j, Y", dateFormat: "U" }}
                                                  className={classnames('due-date-picker', { 'flatpickr-input is-invalid': errors.endDate })} />
                                              )}
                                            />

                                            {errors?.endDate && <FormFeedback className='text-danger'>{errors?.endDate?.message}</FormFeedback>}
                                          </Col>
                                          <Col>{item.label}</Col>
                                        </Col>
                                      )
                                    })
                                  }
                                </Col>
                              }

                            </Col>
                          </Fragment>
                        </Col>
                      </Row>
                    </div>
                  </CardBody>
                </UILoader>
              </div>
            </div>
            <ModalFooter>
              <Col className='d-flex justify-content-between'>
                <Col></Col>
                <Col className='d-flex justify-content-between'>
                  <Col>
                  </Col>
                  <Col>
                    <Button color='warning' onClick={() => { setFormModal(false); setEditClose(false); refreshTaskList(false) }}>Cancel</Button>
                    <Button color='primary' className='ms-50' type='submit'>Submit</Button>
                  </Col>
                </Col>
              </Col>
            </ModalFooter>
          </form >
        </ModalBody>
      </Modal>
    </Fragment>

  )
}

export default RecurringTasks
