// ** React Imports
import { Fragment, useState, useEffect, forwardRef } from 'react'
import { Link, useParams } from 'react-router-dom'
// ** Invoice List Sidebar
import { Can } from '@src/utility/context/Can.js'
// ** Store & Actions
import { getData, deleteTask } from '../store'
import { useDispatch, useSelector } from 'react-redux'
import axios from '@src/configs/axios/axiosConfig'
// ** Third Party Components
import Select from 'react-select'
import ReactPaginate from 'react-paginate'
import { ChevronDown, Edit, MoreVertical, Trash2, Upload } from 'react-feather'

// ** Utils
import AddTask from '@src/views/apps/recurring-task/add/AddCard.js'
import EditTask from '@src/views/apps/recurring-task/edit/EditCard.js'

import { selectThemeColors } from '@utils'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
// ** Reactstrap Imports
import {
  Row,
  Col,
  Card,
  Input,
  Label,
  Button,
  CardBody,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Table,
  Badge
} from 'reactstrap'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import moment from 'moment'

const MySwal = withReactContent(Swal)
// ** Table Header
const CustomHeader = ({ handlePerPage, rowsPerPage, handleFilter, searchTerm, selectedService, refreshTaskList }) => {
  // ** Converts table to CSV
  const [addTaskFlag, setAddTaskFlag] = useState(false)
  const { id } = useParams()

  const refreshList = (val) => {
    if (val) {
      refreshTaskList(true)
    }
    setAddTaskFlag(false)
  }
  return (
    <div className='invoice-list-table-header ps-1 mt-2 mb-75'>
      <Row>

        {
          addTaskFlag &&
          <AddTask id={id} addTaskFlag={addTaskFlag} selectedService={selectedService} refreshList={refreshList} />
        }
        <Col xl='6' md='6' sm='6' className='d-flex align-items-center p-0'>
          <div className='d-flex align-items-center w-100'>
            <label htmlFor='rows-per-page'>Show</label>
            <Input
              className='mx-50'
              type='select'
              id='rows-per-page'
              value={rowsPerPage}
              onChange={handlePerPage}
              style={{ width: '5rem' }}
            >
              <option value='10'>10</option>
              <option value='25'>25</option>
              <option value='50'>50</option>
            </Input>
            <label htmlFor='rows-per-page'>Entries</label>
          </div>
        </Col>
        <Col md='6' sm='6'
          xl='6'
          className='d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'
        >
          <div className='d-flex align-items-center mb-sm-0 mb-1 me-1'>
            <label className='mb-0' htmlFor='search-invoice'>
              Search:
            </label>
            <Input
              id='search-invoice'
              className='ms-50 w-100'
              type='text'
              value={searchTerm}
              onChange={e => handleFilter(e.target.value)}
            />
          </div>

          <div className='d-flex align-items-center table-header-actions'>
            <Can I={'write'} a={'recurring-task'}>
              <Button className='add-new-user' color='primary' onClick={() => { setAddTaskFlag(true) }}>
                Add
              </Button>
            </Can>
            <UncontrolledDropdown className='ms-1'>
              <DropdownToggle color='secondary' caret outline>
                <MoreVertical className='font-small-4 me-50' />
              </DropdownToggle>
              <DropdownMenu>
                <Can I={'import'} a={'task'}>
                  <DropdownItem tag={Link} to={`/import/recurring-task`} className='w-100'>
                    <Upload className='font-small-4 me-50' />
                    <span className='align-middle'>Import</span>
                  </DropdownItem>
                </Can>
              </DropdownMenu>
            </UncontrolledDropdown>

          </div>
        </Col>
      </Row>
    </div>
  )
}

const TaskList = ({ selectedService, tabId }) => {
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector(state => state.recurring_task)
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const { id } = useParams()
  // ** States
  const [sort, setSort] = useState('desc')
  const [searchTerm, setSearchTerm] = useState('')
  const [currentPage, setCurrentPage] = useState(1)
  const [sortColumn, setSortColumn] = useState('createdon')
  const [rowsPerPage, setRowsPerPage] = useState(50)
  const [currentClient, setCurrentClient] = useState({ id: '', name: 'Select Client' })
  const currentService = id
  const [currentUser, setCurrentUser] = useState({ id: '', name: 'Select Employee' })
  const [editTaskFlag, setEditTaskFlag] = useState(false)
  // ** Bootstrap Checkbox Component
  const BootstrapCheckbox = forwardRef((props, ref) => (
    <div className='form-check'>
      <Input type='checkbox' ref={ref} {...props} />
    </div>
  ))
  const priorityOptions = ['', 'Low', 'Medium', 'High']
  const frequencyOptions = ['', 'Monthly', 'Yearly', 'Quarterly', 'Daily', 'Weekly']
  const [selTask, setSelTask] = useState(null)
  const statusObj = [
    '',
    'light-primary',
    'light-success',
    'light-warning'
  ]
  // ** Get data on mount
  useEffect(async () => {
    if (tabId === 'recurring_tasks') {
      setBlock(true)
      await dispatch(
        getData({
          sort,
          sortColumn,
          q: searchTerm,
          user: currentUser.id,
          client: currentClient.id,
          perPage: rowsPerPage,
          page: currentPage,
          recurringFlag: true,
          service: currentService || ''
        })

      )
      setBlock(false)
    }
  }, [tabId])

  const refreshTaskList = async (val) => {
    if (!val) return

    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        user: currentUser.id,
        client: currentClient.id,
        perPage: rowsPerPage,
        page: currentPage,
        recurringFlag: true,
        service: currentService || ''
      })

    )
    setBlock(false)
  }

  const [clientOptions, setClientOptions] = useState([])
  const [userOptions, setuserOptions] = useState([])

  const getClients = () => {
    axios.post('/clients/dropdown', { status: 1 }).then(response => {
      const arr = response.data.clients
      arr.unshift({ id: '', name: 'Select All' })
      setClientOptions(arr)
    })
  }

  const getOrganizationUsers = async () => {
    axios.post('/organizationusers/dropdown').then(response => {
      const arr = response.data.organizationusers
      arr.unshift({ id: '', name: 'Select All' })
      setuserOptions(arr)
    }).catch((err) => {
      console.log(err)
    })
  }

  useEffect(() => {
    if (tabId === 'recurring_tasks') {
      getOrganizationUsers()
      getClients()
      setEditTaskFlag(false)
    }
  }, [tabId])


  // ** Function in get data on page change
  const handlePagination = async page => {
    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        client: currentClient.id,
        perPage: rowsPerPage,
        page: page.selected + 1,
        service: currentService || '',
        recurringFlag: true
      })
    )
    setCurrentPage(page.selected + 1)
    setBlock(false)
  }

  // ** Function in get data on rows per page
  const handlePerPage = async e => {
    const value = parseInt(e.currentTarget.value)
    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        client: currentClient.id,
        service: currentService || '',
        perPage: rowsPerPage,
        page: currentPage,
        user: currentUser.id,
        recurringFlag: true
      })
    )
    setRowsPerPage(value)
    setBlock(false)
  }

  // ** Function in get data on search query change
  const handleFilter = async val => {
    setSearchTerm(val)
    setBlock(true)
    await dispatch(
      getData({
        sort,
        q: val,
        sortColumn,
        perPage: rowsPerPage,
        page: currentPage,
        client: currentClient.id,
        service: currentService || '',
        recurringFlag: true
      })
    )
    setBlock(false)
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      client: currentClient.id,
      service: currentService || '',
      user: currentUser.id,
      recurringFlag: true,
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.data.length > 0) {
      return store.data || []
    } else if (store.data.length === 0 && isFiltered) {
      return []
    }
  }

  const handleSort = async (column, sortDirection) => {
    setSort(sortDirection)
    setSortColumn(column.sortField)
    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        page: currentPage,
        perPage: rowsPerPage,
        client: currentClient.id,
        user: currentUser.id,
        recurringFlag: true,
        service: currentService || ''
      })
    )
    setBlock(false)
  }

  const openEditModal = (val) => {
    setSelTask(val)
    setEditTaskFlag(true)
  }

  const setEditClose = () => {
    setSelTask(null)
    setEditTaskFlag(false)
  }

  const deletefn = (id) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteTask(id))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Task has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }


  return (
    <Fragment>
      <Card className='my-0 pb-0'>
        <CardBody className='my-0 p-0'>
          <Row>
            <Col md='3'>
              <Label for='role-select'>Client</Label>
              <Select
                label={'name'}
                isClearable={false}
                value={currentClient}
                options={clientOptions}
                className='react-select'
                classNamePrefix='select'
                theme={selectThemeColors}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
                onChange={async data => {
                  setCurrentClient(data)
                  setBlock(true)
                  await dispatch(
                    getData({
                      sort,
                      sortColumn,
                      q: searchTerm,
                      client: data.id,
                      page: currentPage,
                      perPage: rowsPerPage,
                      user: currentUser.id,
                      recurringFlag: true,
                      service: currentService || ''
                    })
                  )
                  setBlock(false)
                }}
              />
            </Col>
            <Col md='3'>
              <Label for='role-select'>User</Label>
              <Select
                isClearable={false}
                value={currentUser}
                options={userOptions}
                className='react-select'
                classNamePrefix='select'
                theme={selectThemeColors}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
                onChange={async data => {
                  setCurrentUser(data)
                  setBlock(true)
                  await dispatch(
                    getData({
                      sort,
                      sortColumn,
                      q: searchTerm,
                      client: currentClient.id,
                      page: currentPage,
                      perPage: rowsPerPage,
                      user: data.id,
                      recurringFlag: true,
                      service: currentService || ''
                    })
                  )
                  setBlock(false)
                }}
              />
            </Col>
          </Row>
        </CardBody>
      </Card>
      <UILoader blocking={block} loader={<Loader />}>
        <Card className='overflow-hidden'>
          <CustomHeader
            store={store}
            searchTerm={searchTerm}
            rowsPerPage={rowsPerPage}
            handleFilter={handleFilter}
            handlePerPage={handlePerPage}
            selectedService={selectedService}
            refreshTaskList={refreshTaskList}
          />
          {
            editTaskFlag && <EditTask id={id} editTaskFlag={editTaskFlag} selTask={selTask} selectedService={selectedService} setEditClose={setEditClose} refreshTaskList={refreshTaskList} />
          }
          <div className='react-dataTable'>
            <Table>
              <thead>
                <tr>
                  <th onClick={() => handleSort()}>Client</th>
                  <th>Priority</th>
                  <th>Frequency</th>
                  <th>Date</th>
                  <th>Assignee</th>
                  <th>Reviewer</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {dataToRender().length > 0 && dataToRender().map((row, k) => {
                  return (<tr key={k}>
                    <td>{row.clientname}</td>
                    <td>
                      <Badge color={statusObj[row.priority]}>{priorityOptions[row.priority]}</Badge>
                    </td>
                    <td>{
                      frequencyOptions[row.recurringid]
                    }</td>
                    <td>{
                      moment(row.nextrecurringstartdate, 'x').format('MMM DD, Y')
                    }</td>
                    <td>{
                      row.assignee &&
                      row.assignee.map((obj, key) => { return (<Badge color='light-primary' key={key}>{obj.name}</Badge>) })
                    }</td>
                    <td>
                      {
                        row.reviewer &&
                        row.reviewer.map((obj, key) => { return (<Badge color='light-primary' key={key}>{obj.name}</Badge>) })
                      }
                    </td>
                    <td>
                      <div className='column-action d-flex align-items-center'>
                        <Can I={'write'} a={'recurring-task'}>
                          <Col lg={4}
                            onClick={() => openEditModal(row)} >
                            <Edit
                              className='cursor-pointer ms-1 mt-0' size={16} />
                          </Col>
                        </Can>
                        <UncontrolledDropdown>
                          <DropdownToggle tag='div' className='btn btn-sm'>
                            <MoreVertical size={14} className='cursor-pointer' />
                          </DropdownToggle>
                          <DropdownMenu>
                            <Can I={'delete'} a={'recurring-task'}>
                              <DropdownItem
                                tag='a'
                                href='/'
                                className='w-100'
                                onClick={e => {
                                  e.preventDefault()
                                  deletefn(row.id)
                                }}
                              >
                                <Trash2 size={14} className='me-50' />
                                <span className='align-middle'>Delete</span>
                              </DropdownItem>
                            </Can>
                          </DropdownMenu>
                        </UncontrolledDropdown>
                      </div>
                    </td>
                  </tr>)
                })
                }
              </tbody>
            </Table>

            <CustomPagination />

          </div>
        </Card>
      </UILoader>
    </Fragment>
  )
}

export default TaskList
