// ** React Imports
import { useEffect, useState, Fragment } from 'react'
import { useForm, Controller } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
import { addWokflow, updateWorkflow, listWokflow, deleteWokflow } from '../store'
import classnames from 'classnames'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
const MySwal = withReactContent(Swal)
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
// ** Reactstrap Imports
import { Card, CardBody, Row, Col, ListGroupItem, Input, Label, Button, FormFeedback, Modal, ModalHeader, ModalBody, Form, Table } from 'reactstrap'
import { Link, useParams } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { ReactSortable } from 'react-sortablejs'
import axios from '@src/configs/axios/axiosConfig'
import toast from 'react-hot-toast'
import { Editor } from 'react-draft-wysiwyg'
import draftToHtml from 'draftjs-to-html'
import { EditorState, ContentState, convertFromHTML, convertToRaw } from 'draft-js'
import '@styles/react/libs/editor/editor.scss'
import { Edit, Eye, Trash, Trash2 } from 'react-feather'
const CheckList = ({ tabId }) => {

  const storeLoc = useSelector(state => state.service)
  const [workflowList, setWorkflowList] = useState([])
  const [block, setBlock] = useState(false)
  const [formModal, setFormModal] = useState(false)
  const [data, setData] = useState('')
  const [selctedCard, setSelectedCard] = useState({})
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const dispatch = useDispatch()
  const { id } = useParams()

  const deletefun = (workflowId) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteWokflow({ workflowId, id }))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Checklist has been Updated.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
        return true
      } else if (result.dismiss === MySwal.DismissReason.cancel) {
        return false
      }
    })
  }

  // ** States
  const schema = yup.object().shape({
    createdBy: yup.string().default(userId),
    updatedBy: yup.string().default(userId),
    name: yup.string().required("Please Enter Name"),
    organizationId: yup.string().default(activeOrgId),
    serviceId: yup.string().default(id),
    id: yup.string().default('').nullable(),
    description: yup.string(),
    position: yup.string().default(0)
  })

  const { handleSubmit, formState: { errors }, control, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const resetForm = () => {
    setFormModal(false)
    setValue('id', '')
    setValue('name', '')
    setValue('description', '')
    setData('')
  }

  const onSubmit = async data => {
    if (data.id === '') {
      data['position'] = storeLoc.workFlowLists.length + 1
      await dispatch(addWokflow(data))
    } else {
      await dispatch(updateWorkflow(data))
    }
    resetForm()
  }

  const addModal = (() => {
    setFormModal(true)
  })

  const handleChange = (editorState) => {
    setData(editorState)
    setValue('description', draftToHtml(convertToRaw(editorState.getCurrentContent())))
  }

  useEffect(() => {
    setWorkflowList(storeLoc.workFlowLists)
  }, [storeLoc.workFlowLists])

  useEffect(async () => {
    if (tabId === 'servicechecklist') {
      await dispatch(listWokflow(id))
    }
  }, [tabId])

  const [sortable, setSortable] = useState(true)

  const updatePositionfn = () => {
    setBlock(true)
    axios.post('/workflows/updateposition', { workflows: workflowList })
      .then(() => {
        toast.success('Checklist sorted Successfully', { position: 'bottom-right' })
        setBlock(false)
        setSortable(true)
      })
      .catch(() => { })
  }

  const closeModal = () => {
    resetForm()
  }

  const workFlowEditModal = (workflow) => {
    setFormModal(true)
    setValue('id', workflow.id)
    setValue('name', workflow.name)
    const desc = workflow.description
    setValue('description', desc)
    const temp = EditorState.createWithContent(
      ContentState.createFromBlockArray(
        convertFromHTML(desc)
      )
    )

    setData(temp)
  }

  const handleOnClick = async () => {
    setSortable(!sortable)
  }

  const getRow = (fieldLabel, fieldName, reqflag = true) => {
    return (
      <Col md={12}>
        <Label sm='12' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName}>
          {fieldLabel}
        </Label>
        <Col>
          <Controller
            id={fieldName}
            name={fieldName}
            control={control}
            render={({ field }) => <Input invalid={errors[fieldName] && true} {...field} />}
          />
          {errors[fieldName] && <FormFeedback>{errors[fieldName].message}</FormFeedback>}
        </Col>
      </Col>
    )
  }

  const [showModal, setShowModal] = useState(false)

  const showModalPopUp = (item) => {
    setShowModal(true)
    setSelectedCard(item)
  }

  return (

    <Card>
      <UILoader blocking={block} loader={<Loader />}>
        <CardBody>
          <Modal isOpen={showModal} toggle={() => setFormModal(!showModal)} className='modal-dialog-centered'>
            <ModalHeader toggle={() => { setShowModal(false); setSelectedCard({}) }}>Sub Task</ModalHeader>
            <ModalBody>
              <Col className='d-flex' md={12}>
                <h5>Name :</h5>
                <p className='ms-1'>{selctedCard.name}</p>
              </Col>
              <Col className='mt-50'>
                <h5 className=''>Description:</h5>
                <div className='white-space-wrap' dangerouslySetInnerHTML={{ __html: selctedCard?.description }} >
                </div>
              </Col>
            </ModalBody>
          </Modal>
          <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
            <ModalHeader toggle={() => closeModal()}>Sub Task</ModalHeader>
            <ModalBody>
              {
                Object.keys(errors).map((key) => {
                  return <FormFeedback key={key}>${errors[key]?.message}</FormFeedback>
                })
              }
              <Row tag={Form} className='gx-2 gy-1' onSubmit={handleSubmit(onSubmit)}>
                {getRow('Sub Task Name', 'name', true)}
                <Col md={12}>
                  <Label className='form-label' for='description'>
                    Description
                  </Label>
                  <div id='message-editor'>
                    <Editor
                      placeholder='Message'
                      toolbarClassName='rounded-0'
                      wrapperClassName='toolbar-bottom'
                      editorState={data}
                      onEditorStateChange={handleChange}
                      editorClassName='rounded-0 border-1'
                    />
                  </div>
                  {errors.description && <FormFeedback>{errors.description.message}</FormFeedback>}
                </Col>

                <Col className='mt-2 pt-1' xs={12}>
                  <Button type='submit' className='me-1' color='primary'>
                    Submit
                  </Button>
                  <Button className='me-1' color='warning' onClick={() => closeModal()}>
                    Cancel
                  </Button>
                </Col>

              </Row>
            </ModalBody>
          </Modal>


          <Row id='dd-with-handle'>
            <Row className='mb-1 d-fex'>
              <p className='text-primary col-6'>*Drag and Drop to change position</p>
              <Col sm='6' className='px-0 d-flex justify-content-end'>
                <Button color='primary' className='me-1' tag={Link} to={'/import/subtask'}> Import</Button>
                {sortable && <Button color='primary' onClick={() => addModal()} outline size='sm'>Add Sub Task</Button>}
                {sortable && <Button outline color={'warning'} className='ms-1' onClick={() => { handleOnClick() }}>Change Position</Button>}
              </Col>
            </Row>
            <Table md={12} className=''>
              <thead>
                <tr>
                  <th>Sub Task</th>
                  <th>Sub Task Name</th>
                  <th>Description</th>
                  <th className='text-end'>actions</th>
                </tr>
              </thead>
              {
                !sortable &&
                <ReactSortable tag='tbody' list={workflowList} setList={setWorkflowList} onSort={() => { updatePositionfn() }}>
                  {workflowList.map((item) => {
                    return (
                      <tr className='cursor-pointer' key={item.id}>
                        <td>Sub Task {item.position}</td>
                        <td>{item.name.length > 39 ? `${item.name.substr(0, 40)}` : item.name}</td>
                        <td>
                          <div className='white-space-wrap' dangerouslySetInnerHTML={{ __html: item?.description?.replace(/(<([^>]+)>)/ig, '').substr(0, 40) }} >
                          </div>
                        </td>
                        <td className='text-end'>
                          <Eye size={16} onClick={() => { showModalPopUp(item) }} />
                          <Edit className='ms-50' size={16} onClick={() => { workFlowEditModal(item) }} />
                          <Trash2 className='ms-50' size={16} onClick={() => { deletefun(item.id) }} />
                        </td>
                      </tr>
                    )
                  })}
                </ReactSortable>
              }
              {
                sortable &&
                <tbody>
                  {workflowList.map((item) => {
                    return (
                      <tr className='' key={item.id}>
                        <td className='cursor-pointer' onClick={() => { showModalPopUp(item) }}>Sub Task {item.position}</td>
                        <td className='cursor-pointer mt-0' onClick={() => { showModalPopUp(item) }} >{item.name.length > 39 ? `${item.name.substr(0, 40)}` : item.name}</td>
                        <td className='cursor-pointer' onClick={() => { showModalPopUp(item) }}>
                          <div className='white-space-wrap' dangerouslySetInnerHTML={{ __html: item?.description?.replace(/(<([^>]+)>)/ig, '').substr(0, 40) }} >
                          </div>
                        </td>
                        <td className='text-end'>
                          <Eye size={16} onClick={() => { showModalPopUp(item) }} />
                          <Edit className='ms-50' size={16} onClick={() => { workFlowEditModal(item) }} />
                          <Trash2 className='ms-50' size={16} onClick={() => { deletefun(item.id) }} />
                        </td>
                      </tr>
                    )
                  })
                  }
                </tbody>
              }
            </Table>

          </Row>
        </CardBody>
      </UILoader>
    </Card >

  )
}

export default CheckList
